import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom'
import { Modal } from "react-bootstrap";
import {prettyBalance} from "../../utils/common"
import near from '../../lib/near'

const TransferModal = (props) => {
    const [address, setAddress] = useState("");

    const handleTransfer = async() => {
      const accountExist = await near.doesAccountExist(address);
      if(!accountExist) {
          alert('Account Not Exists')
          return
      }
      props.handleTransfer(address)
    }
  
    return (
        <Modal
            show={props.show}
            onHide={props.onHide}
            centered
        >
        <Modal.Header closeButton></Modal.Header>

            <div className="modal-body space-y-20 pd-40">
                <h3>Confirm Transfer</h3>
                    <p className="text-center"><span className="price color-popup">{props.title} </span>
                </p>
                <input type="text" className="form-control"
                    placeholder="Receiver address" onChange={(e) => setAddress(e.target.value)} value={address}/>
                <button className="btn btn-primary" onClick={()=>handleTransfer()} data-toggle="modal" data-target="#popup_bid_success" data-dismiss="modal" aria-label="Close"> Transfer</button>
            </div>
        </Modal>
    );
};

export default TransferModal;
