import React , { useState , Fragment, useEffect } from 'react';
import {Link} from 'react-router-dom';
import { Tab, Tabs, TabList, TabPanel  } from 'react-tabs';
import axios from 'axios';
import 'react-tabs/style/react-tabs.css';
import CardModal from '../CardModal';
import Select from '../../Common/Select';
import InfiniteScroll from 'react-infinite-scroll-component';
import Spinner from 'react-bootstrap/Spinner';

const TOKENS_PER_PAGE = 5;

const TodayPicks = () => {
    const [collectionSelect, SetCollectionSelect] = useState('All Collections');
    const [query, setQuery] = useState({page:0});
    const [collections, setCollections] = useState([]);
    const [loading, setLoading] = useState(false);
    const [hasMore, setHasMore] = useState(false); // Can load more tokens

    const [modalShow, setModalShow] = useState(false);

    const getCollections = async (query) => {
        try {
            const resCollections = await axios.get(`${process.env.REACT_APP_API_URL}/collections`, {
                params: {
                    ...query.params,
                    __skip: query.page * TOKENS_PER_PAGE,
                    __limit: TOKENS_PER_PAGE,
                },
		    })

            setCollections(resCollections.data.data.results);
    
            if (resCollections.data.data.results.length >= TOKENS_PER_PAGE) setHasMore(true);
    
        } catch (error) {
            console.log(error);
            setCollections([]);
        }
    
        setLoading(false);
    };

    const fetchMoreCollections = async (query) => {
        try {
            const resCollections = await axios.get(`${process.env.REACT_APP_API_URL}/collections`, {
                params: {
                    ...query.params,
                    __skip: (query.page + 1) * TOKENS_PER_PAGE,
                    __limit: TOKENS_PER_PAGE,
                },
		    })

            const data = await resCollections.data.data.results;
    
            setQuery({ ...query, page: query.page + 1 });
        
            if (data.length < TOKENS_PER_PAGE) setHasMore(false);
            const newData = [...collections, ...data]
            console.log(newData)
            setCollections(newData);
        } catch (error) {
          console.log(error);
        }

    };

    const beautifyAddr = (addr) => {
        if(addr ==  null)
            return ''
        if(addr.length > 16){
            let tmpstr = addr.slice(0,4) + '...' + addr.slice(addr.length - 6, addr.length)
            return tmpstr;
        }
        return addr
    }

    useEffect(() => {
        setLoading(true);
        getCollections(query);
    
    }, [query.params]);

    useEffect(() => {
        if(collectionSelect=="All Collections") {
            setQuery({page: 0})
        } else if(collectionSelect=="Newest") {
            setQuery({page: 0, params: {is_newer: true}})
        } else if(collectionSelect=="Oldest") {
            setQuery({page: 0, params: {is_older: true}})
        } else if(collectionSelect=="Trending") {
            setQuery({page: 0, params: {is_trending: true}})
        }
    
    }, [collectionSelect]);

    return (
        <Fragment>
            <div className="tf-section sc-explore-2">
                <div className="themesflat-container">
                
                    <div className="row">
                        <div className="col-md-12">
                            <div className="seclect-box style3">
                                <Select value={collectionSelect} setValue={SetCollectionSelect} options={['All Collections', 'Newest', 'Oldest', 'Trending']}/>  
                            </div>
                        </div>
                        {loading && (
                            <Spinner animation="border" role="status" variant="primary" style={{width:"100px", height:"100px", borderWidth: "thick", position:"relative", left: "50%"}}>
                                <span className="visually-hidden">Loading...</span>
                            </Spinner>
                        )}
                        {!loading && collections.length>0 && 
                        <InfiniteScroll
                            dataLength={collections.length}
                            next={() => fetchMoreCollections(query)}
                            hasMore={hasMore}
                            loader={
                                <Spinner animation="border" role="status" variant="primary" style={{width:"40px", height:"40px", position:"relative", left: "50%"}}>
                                    <span className="visually-hidden">Loading...</span>
                                </Spinner>
                            }
                            className="infinite-scroll-grid flex pt-5"
                            scrollThreshold={0.9}
                            style={{ height:'auto', overflow: 'hidden'}}
                        >
                            {
                            collections.map((item, index)=>(
                                <div key={index} className="fl-item col-box-20 col-lg-3 col-md-4 col-sm-6">
                                    <div className={`sc-card-product ${item.feature ? 'comingsoon' : '' } `}>                               
                                        <div className="card-media">
                                            <Link to={`/collection/${item.contract_id}`}>
                                                <figure style={{ position: 'relative'}}>
                                                <img src={item.bgimg_url} className={'collection-bg img-fluid'} alt="Axies" style={{ objectFit: 'cover'}} />
                                                    <figcaption style={{ position: 'absolute', top: '8px', right: '16px', zIndex: 1}}>
                                                        {
                                                            item.is_trending &&
                                                            <span style={{ fontSize: '30px', color: '#e8467c' }}><i className="far fa-chart-bar"></i></span>
                                                        }
                                                    </figcaption>
                                                </figure>
                                            </Link>
                                            <div className="coming-soon">{'jjj'}</div>
                                        </div>
                                        <div className="sc-author-box style-4 avatar d-flex justify-content-center pb-4 ps-4" style={{ marginTop: '-50px', marginBottom: '0px', boxShadow: 'none', background: 'none'}}>
                                            <img src={item.banimg_url} className='img-fluid' style={{ height: '50px', borderRadius: '50%', zIndex: 200 }} alt="Axies" />
                                            {
                                                item.verified ? 
                                                <div className='author-avatar' style={{ zIndex: 200, marginBottom: '-60px' }}>
                                                    <div className="badge" style={{ height: '16px', width: '16px', lineHeight: '16px' }}></div>
                                                </div>
                                                :
                                                <></>
                                            }
                                        </div>
                                        <div className="card-title justify-content-center mb-4">
                                            <h5><Link to={`/collection/${item.contract_id}`}>{item.name}</Link></h5>
                                        </div>
                                        <div className="meta-info">
                                            <h6 style={{ fontWeight: 300 }}>Creator</h6>
                                            <h6><Link to={`/users/${item.creator_id}`}>{beautifyAddr(item.creator_id)}</Link></h6>
                                        </div>
                                        <div className="meta-info mt-3">
                                            <h6 style={{ fontWeight: 300 }}>Items</h6>
                                            <h6>{item.total_count}</h6>
                                        </div>
                                    </div>
                                </div>
                            )  
                            )}
                        </InfiniteScroll>
                        }
                    </div>
                    
                </div>
            </div>
            <CardModal
                show={modalShow}
                onHide={() => setModalShow(false)}
            />
        </Fragment>
    );
}

export default TodayPicks;
