import React , { useState , Fragment, useEffect } from 'react';
import {Link} from 'react-router-dom';
import 'react-tabs/style/react-tabs.css';
import {prettyTruncate} from '../../../utils/common'

const CollectionView = ({data}) => {
    const beautifyAddr = (addr) => {
        if(addr ==  null)
            return ''
        if(addr.length > 16){
            let tmpstr = addr.slice(0,4) + '...' + addr.slice(addr.length - 6, addr.length)
            return tmpstr;
        }
        return addr
    }
    return (
        <Fragment>
            <div className="tf-section sc-explore-2 py-0">
                <div className="themesflat-container">
                    <div className="row">
                    {data?.length>0 && 
                        data.map((item)=>(
                            <div key={item.contract_id} className="fl-item col-xl-3 col-lg-4 col-md-6 col-sm-6">
                                <div className={`sc-card-product ${item.feature ? 'comingsoon' : '' } `}>                               
                                    <div className="card-media">
                                        <Link to={`/collection/${item.contract_id}`}><img src={item.bgimg_url} className={'collection-bg img-fluid'} alt="Axies" style={{ objectFit: 'cover'}} /></Link>
                                        <div className="coming-soon">{'jjj'}</div>
                                    </div>
                                    <div className="sc-author-box style-4 avatar d-flex justify-content-center" style={{ marginTop: '-50px', marginBottom: '0px', boxShadow: 'none', background: 'none', paddingBottom: '20px' }}>
                                        <img src={item.banimg_url} className='img-fluid' style={{ height: '50px', borderRadius: '50%', zIndex: 200 }} alt="Axies" />
                                        <div className='author-avatar' style={{ zIndex: 200, marginBottom: '-60px' }}>
                                            <div className="badge" style={{ height: '16px', width: '16px', lineHeight: '16px' }}></div>
                                        </div>
                                    </div>
                                    <div className="card-title justify-content-center mb-5">
                                        <h5><Link to={`/collection/${item.contract_id}`}>{item.name}</Link></h5>
                                    </div>
                                    <div className="meta-info">
                                        <h6 style={{ fontWeight: 300 }}>Creator</h6>
                                        <h6><Link to={`/users/${item.creator_id}`}>{beautifyAddr(item.creator_id)}</Link></h6>
                                    </div>
                                    <div className="meta-info mt-3">
                                        <h6 style={{ fontWeight: 300 }}>Items</h6>
                                        <h6>{item.total_count}</h6>
                                    </div>
                                </div>
                            </div>
                        )
                    )}
                    </div>
                </div>
            </div>
        </Fragment>
    );
}

export default CollectionView;
