import React, { useState, useEffect } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import { useWalletSelector } from '../hooks/WalletSelectorContext'
import axios from 'axios'
import { BreadcrumbItem, Form, InputGroup, ButtonGroup, ToggleButton } from 'react-bootstrap'
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import defaultitemImgUrl from '../assets/images/avatar/avt-2.jpg';

var FormData = require('form-data');

const EditFeaturedItem = () => {
    const navigate = useNavigate();
    const { itemid } = useParams();
    //user collection field
    const [itemImg, setitemImg] = useState("https://dl.airtable.com/.attachmentThumbnails/8d6a4dbcaadd2a99d4a8ddcaf5a9497b/dc6de58e");
    const [title, settitle] = useState("");
    const [linkedurl, setlinkedurl] = useState("");
    const [description, setdescription] = useState("");
    const [radioValue, setRadioValue] = useState('1');
    const radios = [
        { name: 'Go To LaunchPad', value: '1' },
        { name: 'Explore Collection', value: '2' },
    ];

    //update user collection field button function
    const updatefeatureditem = async () => {
        let status = false;
        status = checkDataField([ title, description, itemImg])
        if (status) 
        {
            let updateData = { itemImg, title, linkedurl, description, itemstatus: radioValue, itemid}
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/updatefeatureditem`, {
                params: {
                    ...updateData,
                },
            })
            if(res?.data?.data == 'success'){
                toast('Successfully Updated!',  {
                    position: toast.POSITION.BOTTOM_RIGHT,
                    className: 'foo-bar'
                })
                if(itemid == null){
                    navigate('/admin/featured')
                }
            }
        }
    }

    const deletefeatureditem = async () => {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/deletefeatureditem`, {
            params: {
                itemid,
            },
        })
        if(res?.data?.data == 'success'){
            toast('Successfully Deleted!',  {
                position: toast.POSITION.BOTTOM_RIGHT,
                className: 'foo-bar'
            })
            navigate('/admin/featured')
        }
    }

    const uploaditemImg = (ev) => {
        const file = ev.target.files[0];
        const url = `https://api.pinata.cloud/pinning/pinFileToIPFS`;
        const formData = new FormData();
        formData.append("file",file);
        return axios.post(url,formData,{
            headers:{
                'Content-Type': `multipart/form-data; boundary= ${formData._boundary}`,
                'pinata_api_key':'fe3060199e1632e4d52a',
                'pinata_secret_api_key':'1f4a05527f4e641e6fe037affea5fb577196dd8c34f7db093c0d27102f3e6ae7',
            }
        }).then(function (response) {
                setitemImg("https://nearnaut.mypinata.cloud/ipfs/" + response.data.IpfsHash)
                console.log('itemImg', "https://nearnaut.mypinata.cloud/ipfs/" + response.data.IpfsHash)
        }).catch(function (error) {
            console.log('cant upload itemImg',error)
        });
    }

    const deleteitemImg = () => {
        setitemImg("")
    }

    const checkDataField = (datalist) => {
        for (let index = 0; index < datalist.length; index++) {
            const element = datalist[index];
            if(element == ""){
                return false;
            }
        }
        return true;
    }
    
    //Init data
    useEffect( async () => {
        if(itemid){
            let initdata = await axios.get(`${process.env.REACT_APP_API_URL}/getfeatureditem`, {
                params: {
                    itemid,
                },
            })
            if(initdata?.data.data != null){
                //get information of address
                const maindata = initdata.data.data.results;
                settitle(maindata.title);
                setdescription(maindata.description);
                setitemImg(maindata.itemImg);
                setlinkedurl(maindata.linkedurl);
                setRadioValue(maindata.itemstatus);
            }
        }
    },[]);

    return (
        <div>
            <Header />
            <section className="flat-title-page inner">
                <div className="overlay"></div>
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="page-title-heading mg-bt-12">
                                <h1 className="heading text-center">{ itemid ? 'Edit Featured Item' : 'Create Featured Item' }</h1>
                            </div>
                        </div>
                    </div>
                </div>                    
            </section>
            <div className="tf-create-item tf-section">
                <div className="themesflat-container">
                    <div className="row">
                         <div className="col-xl-4 col-lg-4 col-md-12 col-12">
                            <div className="sc-card-profile text-center">
                                <div className="card-media">
                                    {itemImg=="" ? 
                                    <img id="profileimg" src={defaultitemImgUrl} alt="Axies" />
                                    :
                                    <img id="profileimg" src={itemImg} alt="Axies" style={{ margin: '20px 0px'}}/>
                                    }
                                </div>
                                <div id="upload-profile">
                                    <Link to="#" className="btn-upload">Upload Featured Item Image</Link>
                                    <input id="tf-upload-img" type="file" name="profile" onChange={(ev) => uploaditemImg(ev)}/>
                                </div>
                                <button className="btn-upload style2 border-0" onClick={() => deleteitemImg()}>
                                    Delete
                                </button>
                            </div>
                         </div>
                         <div className="col-xl-8 col-lg-8 col-md-12 col-12">
                            <div className="form-upload-profile">
                                <div className="form-profile">
                                    <div>
                                        <div>
                                            <h4 className="title-create-item">Item Information</h4>
                                            <InputGroup hasValidation className='mb-5'>
                                                <InputGroup.Text className='col-3'><p style={{ fontSize: '1.5rem'}} className='m-auto'>Title</p></InputGroup.Text>
                                                <Form.Control type="text" placeholder="Trista Francis" value={title} onChange={(ev) => settitle(ev.target.value)} required isInvalid={title=="" ? true : false} />
                                                <Form.Control.Feedback type="invalid">
                                                    <div style={{ fontSize: '1.3rem'}}>Please type item title.</div>
                                                </Form.Control.Feedback>
                                            </InputGroup>
                                            <InputGroup hasValidation className='mb-5'>
                                                <InputGroup.Text className='col-3'><p style={{ fontSize: '1.5rem'}} className='m-auto'>Website</p></InputGroup.Text>
                                                <Form.Control type="text" placeholder="Axies.Trista Francis.com/" value={linkedurl} onChange={(ev) => setlinkedurl(ev.target.value)} />
                                                <Form.Control.Feedback type="invalid">
                                                    <div style={{ fontSize: '1.3rem'}}>Please type linked url.</div>
                                                </Form.Control.Feedback>
                                            </InputGroup>
                                            <InputGroup hasValidation className='mb-5'>
                                                <InputGroup.Text className='col-3'><p style={{ fontSize: '1.5rem'}} className='m-auto'>Description</p></InputGroup.Text>
                                                <Form.Control as="textarea" tabIndex="4" rows="5" value={description} onChange={(ev) => setdescription(ev.target.value)} required isInvalid={description =="" ? true : false} style={{fontSize: '18px'}}/>
                                                <Form.Control.Feedback type="invalid">
                                                    <div style={{ fontSize: '1.3rem'}}>Please type your description information.</div>
                                                </Form.Control.Feedback>
                                            </InputGroup>
                                            <div className='mb-3'>
                                                <p></p>
                                                <ButtonGroup>
                                                    {radios.map((radio, idx) => (
                                                    <ToggleButton
                                                        key={idx}
                                                        id={`radio-${idx}`}
                                                        type="radio"
                                                        variant={idx % 2 ? 'outline-success' : 'outline-danger'}
                                                        name="radio"
                                                        value={radio.value}
                                                        checked={radioValue === radio.value}
                                                        onChange={(e) => setRadioValue(e.currentTarget.value)}
                                                        style={{ fontSize: '18px' }}
                                                        className='px-5 py-2'
                                                    >
                                                        {radio.name}
                                                    </ToggleButton>
                                                    ))}
                                                </ButtonGroup>
                                            </div>
                                        </div>
                                    </div>
                                    <button className="tf-button-submit mg-t-15 me-4" onClick={ () => updatefeatureditem() }>
                                        { itemid ? 'Update Item': 'Create Item'}
                                    </button>
                                    {
                                        itemid &&
                                        <button className="tf-button-submit mg-t-15 me-4" onClick={ () => deletefeatureditem() }>
                                            Delete Item
                                        </button>
                                    }
                                    <button  className="tf-button-submit mg-t-15" onClick={ () => navigate('/admin/featured')}>Goto Previous Page</button>
                                </div>
                            </div>
                         </div>
                    </div>
                </div>
            </div>
            <Footer />
        </div>
    );
}

export default EditFeaturedItem;
