import React , {useState, useEffect} from 'react';
import { Link, useNavigate } from 'react-router-dom';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import axios from 'axios'
import { useWalletSelector } from '../hooks/WalletSelectorContext'

const Users = () => {
    const navigate = useNavigate();
    const { accountId, setCommonModal } = useWalletSelector()
    const [visible , setVisible] = useState(5);
    const showMoreItems = () => {
        setVisible((prevValue) => prevValue + 5);
    }

    const verify = async (address) => {
        console.log(address, 'verify')
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/verify`, {
            params: {
                address,
                "verified": true
            },
        })
        if(res?.data.data == "success"){
            let newdata = [];
            for (let i = 0; i < userdata.length; i++) {
                let element = userdata[i];
                if(element.address == address)
                    element.verified = true;
                newdata.push(element)
            }
            setuserdata(newdata)
        }
    }

    const unverify = async (address) => {
        console.log(address, 'unverify')
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/verify`, {
            params: {
                address,
                "verified": false
            },
        })
        if(res?.data.data == "success"){
            let newdata = [];
            for (let i = 0; i < userdata.length; i++) {
                let element = userdata[i];
                if(element.address == address)
                    element.verified = false;
                newdata.push(element)
            }
            setuserdata(newdata)
        }
    }

    const [userdata, setuserdata] = useState([]);

    //Init data
    useEffect( async () => {
        if(!accountId) {
            navigate("/");
            return
        }
        let initdata = await axios.get(`${process.env.REACT_APP_API_URL}/getUsers`, {
            params: {
            },
        })
        console.log('init data', initdata)
        if(initdata?.data.data != null){
            setuserdata(initdata.data.data);
        }
    },[accountId]);

    return (
        <div>
            <Header />
            <section className="flat-title-page inner">
                <div className="overlay"></div>
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="page-title-heading mg-bt-12">
                                <h1 className="heading text-center">Users</h1>
                            </div>
                        </div>
                    </div>
                </div>                    
            </section>
            <section className="tf-activity s1 tf-section">
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-lg-12 col-md-12 col-12">
                            <div id="side-bar" className="side-bar style-2">
                                <div className="widget widget-search mgbt-24">
                                    <form action="#">
                                        <input type="text" placeholder="Enter your word art" required />
                                        <button><i className="icon-fl-search-filled"></i></button>
                                    </form>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-12 col-md-12 col-12">
                            {
                                userdata.slice(0,visible).map((item,index) =>(
                                    <div className="sc-card-activity style1" key={index}>
                                        <div className="content sc-author-box style-2 w-100">
                                            <div className="media d-flex align-items-center col-12 col-md-1 author-avatar">
                                                <img className='img-fluid' style={{ objectFit: 'contain' }} src={item.photo} alt="Axies" />
                                                {item.verified? <div className="badge"></div> : <></>}
                                            </div>
                                            <div className="infor col-12 col-md-5">
                                                <h3> <Link to="/item-details-01">{item.displayName}</Link></h3>
                                                <div className="status">{item.status} <span className="author">{item.address}</span></div>
                                                <div className="time">{item.bio}</div>
                                            </div>
                                            <div className='infor col-6 col-md-3'>
                                                <div className="status">{item.status} <span className="author">{item.email}</span></div>
                                                <ul className='d-flex align-items-center justify-content-evenly'>
                                                    <li><Link to={item.twitter} target="_blank"><i className="fab fa-twitter" style={{ fontSize: '3rem'}}></i></Link></li>
                                                    <li><Link to={item.facebook? item.facebook : ""} target="_blank"><i className="fab fa-facebook" style={{ fontSize: '3rem'}}></i></Link></li>
                                                    <li><Link to={item.customeUrl} target="_blank"><i className="fa fa-globe" style={{ fontSize: '3rem'}}></i></Link></li>
                                                </ul>
                                            </div>
                                            <div className='infor col-6 col-md-2'>
                                                {
                                                    item.verified == true ? 
                                                    <button className="tf-button-submit mg-t-15" onClick={ () => unverify(item.address) }>
                                                        UnVerify
                                                    </button>
                                                    :
                                                    <button className="tf-button-submit mg-t-15" onClick={ () => verify(item.address) }>
                                                        Verify
                                                    </button>
                                                }
                                            </div>
                                        </div>
                                    </div>
                                ))
                            }
                            {
                                visible < userdata.length && 
                                <div className="col-md-12 wrap-inner load-more text-center"> 
                                    <Link to="#" id="load-more" className="sc-button loadmore fl-button pri-3 mt-10" onClick={showMoreItems}><span>Load More</span></Link>
                                </div>
                            }
                        </div>
                    </div>
                </div>
            </section>
            <Footer />
        </div>
    );
}

export default Users;
