import React , {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import defaultPhotoUrl from '../assets/images/avatar/avt-2.jpg';
import axios from 'axios'
import InfiniteScroll from 'react-infinite-scroll-component';
import Spinner from 'react-bootstrap/Spinner';
import Select from '../components/Common/Select';
import { prettyBalance, prettyTruncate, parseFilter, parseTimeForFilter, unParseFilter, parseDateFilter } from '../utils/common'
import { toast } from 'react-toastify';

const EditFeatured = () => {
    const [featureditem, setfeatureditem] = useState([]);
    const [loading, setloading] = useState(false);
    const [query, setQuery] = useState({page:0});
    const [changed, setchanged] = useState(1);

    const getfeatureditems = async () => {
        setloading(true);
        setQuery({page: 0, params: {}})
        let initdata = await axios.get(`${process.env.REACT_APP_API_URL}/getfeatureditem`, {
            params: {},
        })
        if(initdata?.data.data != null){
            setfeatureditem(initdata.data.data.results);
        }
        setloading(false);
    }

    const onDelete = async (_id) => {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/deletefeatureditem`, {
            params: {
                itemid : _id,
            },
        })
        if(res?.data?.data == 'success'){
            toast('Successfully Deleted!',  {
                position: toast.POSITION.BOTTOM_RIGHT,
                className: 'foo-bar'
            })
            setchanged(Math.random()%10);
        }
    }

    useEffect( () => {
        getfeatureditems();
    },[changed]);

    return (
        <div>
            <Header />
            <section className="flat-title-page inner">
                <div className="overlay"></div>
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="page-title-heading mg-bt-12">
                                <h1 className="heading text-center">Edit Featured Items</h1>
                            </div>
                        </div>
                    </div>
                </div>                    
            </section>
            <section className="tf-activity s1 tf-section">
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-lg-12 col-md-12 col-12">
                            <div className='mb-5'>
                                <a href='/admin/featured/create'><button onClick={() => console.log('add new item')}>Add Featured Item</button></a>
                            </div>
                            <div className="table-ranking">
                                <div>
                                {loading && (
                                    <Spinner animation="border" role="status" variant="primary" style={{width:"100px", height:"100px", borderWidth: "thick", position:"relative", left: "50%"}}>
                                        <span className="visually-hidden">Loading...</span>
                                    </Spinner>
                                )}
                                {!loading && (
                                    featureditem?.map((item,index) => (
                                        <div key={index} className="fl-item2 px-2" style={{ width: '95%' }}>
                                            <div className="item flex">
                                                <div className="infor-item flex col-3">
                                                    <div className="media d-flex">
                                                        <img className="rounded" style={{ objectFit: 'cover' }} src={item?.itemImg? item.itemImg : defaultPhotoUrl} alt="Axies" />
                                                    </div>
                                                    <div className="content-collection align-self-center px-4">
                                                        <h5 className="title"><Link to={`/admin/featured/edit/${item?._id}`}>{prettyTruncate(item?.title, 30)}</Link></h5>
                                                    </div>
                                                </div>
                                                <div className="col-3">
                                                    <span style={{fontSize: '17px'}}>{item?.linkedurl}</span>
                                                </div>
                                                <div className="col-3">
                                                    <span style={{fontSize: '17px'}}>{ prettyTruncate(item?.description, 30,) }</span>
                                                </div>
                                                <div className="col-3 d-flex justify-content-around">
                                                    <button><Link to={`/admin/featured/edit/${item?._id}`}>Edit</Link></button>
                                                    <button onClick={() => onDelete(item?._id)}>Delete</button>
                                                </div>
                                            </div>
                                        </div>
                                    ))
                                )}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Footer />
        </div>
    );
}

export default EditFeatured;
