import Home07 from "./Home07";
import Explore02 from "./Explore02";
import Explore04 from "./Explore04";
import ItemDetails01 from "./ItemDetails01";
import Authors01 from "./Authors01";
import Authors02 from "./Authors02";
import CreateItem from "./CreateItem";
import EditProfile from "./EditProfile";
import EditCollection from "./EditCollection";
import Ranking from "./Ranking";
import NoResult from "./NoResult";
import FAQ from "./FAQ";
import Users from "./Users";
import MyActivity from "./MyActivity";
import EditFeatured from "./EditFeatured";
import EditFeaturedItem from "./EditFeaturedItem";

const routes = [
  { path: '/', component: <Home07 />},
  { path: '/collection/:collectionId', component: <Explore04 />},
  { path: '/collection/:collectionId/edit', component: <EditCollection />},
  { path: '/collections', component: <Explore02 />},
  { path: '/users', component: <Authors01 />},
  { path: '/collection/:collectionId/:tokenId', component: <ItemDetails01 />},
  { path: '/activity', component: <MyActivity />},
  { path: '/users/:creatorId', component: <Authors02 />},
  { path: '/create-item', component: <CreateItem />},
  { path: '/edit-profile', component: <EditProfile />},
  { path: '/ranking', component: <Ranking />},
  { path: '/no-result', component: <NoResult />},
  { path: '/faq', component: <FAQ />},
  { path: '/admin/users', component: <Users />},
  { path: '/admin/featured', component: <EditFeatured />},
  { path: '/admin/featured/create', component: <EditFeaturedItem />},
  { path: '/admin/featured/edit/:itemid', component: <EditFeaturedItem />},
]

export default routes;