import logo from '../../assets/images/icon/AstroMarket.png'

const Loader = ({ isLoading }) => {
	return (
		<div
			className={`fixed-top fixed-bottom justify-content-center align-items-center ${
				isLoading ? `d-flex` : `d-none`
			}`}
			style={{backgroundColor: "#444452f2", zIndex: "99999"}}
		>
			<LogoBounce />
		</div>
	)
}


export const LogoBounce = ({ className = '', width = 300 }) => (
	<div className={`animated infinite pulse ${className}`}>
		 <img className="avatar" src={logo} alt="avatar" />
	</div>
)

export default Loader
