import React, { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom'
import axios from 'axios';
import Chart from 'react-apexcharts';
import { ButtonGroup, ToggleButton } from 'react-bootstrap';
import { prettyBalance, prettyTruncate, parseFilter, parseTimeForFilter, unParseFilter, parseDateFilter1 } from '../utils/common'
import InfiniteScroll from 'react-infinite-scroll-component';
import Spinner from 'react-bootstrap/Spinner';
import defaultPhotoUrl from '../assets/images/avatar/avt-2.jpg';

const CollectionActivity = () => {
    const {collectionId} = useParams();
    const [radioValue, setRadioValue] = useState('1');
    const ACTIVITIES_PER_PAGE = 10;

    const [activities, setactivities] = useState([]);
    const [loading, setloading] = useState(false);
    const [query, setQuery] = useState({page:0});
    const [hasMore, setHasMore] = useState(false); // Can load more tokens

    const staticChartOption = {
        chart: {
            height: 350,
            type: 'line',
            toolbar: {
                show: false,
            },
            zoom:{
                enabled: false,
            }
        },
        stroke: {
            show: true,
            curve: 'smooth',
            lineCap: 'butt',
            colors: undefined,
            width: 1,
            dashArray: 0,   
        },
        fill: {
            type: 'gradient',
            gradient: {
                shade: 'dark',
                type: "vertical",
                shadeIntensity: 0.1,
                gradientToColors: undefined, // optional, if not defined - uses the shades of same color in series
                inverseColors: true,
                opacityFrom: 1,
                opacityTo: 0.1,
                stops: [0, 70, 100],
                colorStops: []
            }
        },
        title: {
            text: 'Floor Price & Volume (Near)',
            style: {
                    fontSize:  '18px',
                    color: '#7A798A'
            }
        },
        dataLabels: {
            enabled: false,
            enabledOnSeries: [1]
        },
        labels: ['01 Jan 2001', '02 Jan 2001', '03 Jan 2001', '04 Jan 2001', '05 Jan 2001', '06 Jan 2001', '07 Jan 2001', '08 Jan 2001', '09 Jan 2001', '10 Jan 2001', '11 Jan 2001', '12 Jan 2001'],
        xaxis: {
            type: 'datetime',
            labels: {
                style: {
                    colors: '#7A798A'
                }
            }
        },
        yaxis: [{
            title: {
                text: '',
            },
            labels: {
                style: {
                    colors: '#7A798A'
                }
            }
        }, {
            // show:false,
            opposite: true,
            title: {
                text: '',
                logBase: 10
            },
            labels: {
                style: {
                    colors: '#7A798A'
                }
            }
        }],
        colors: ['#008FFB','#c0e4ff'],
        theme: {
            mode: 'light', 
        }
    }

    const fetchMoreActivities = async () => {
        try {
            let tmpfilters = []
            for (let i = 0; i < dataFilter.length; i++) {
                if(dataFilter[i].state)
                    tmpfilters.push(unParseFilter(dataFilter[i].name))
            }
            if(tmpfilters.length == 0)
                setQuery({page: 0})
            if(tmpfilters.length > 0)
                setQuery({page: 0, params: { filter:tmpfilters.join('||') }})
            const resCollections = await axios.get(`${process.env.REACT_APP_API_URL}/activities`, {
                params: {
                    ...query.params,
                    datefilter: parseDateFilter1(radioValue),
                    nft_contract_id: collectionId,
                    __skip: (query.page + 1) * ACTIVITIES_PER_PAGE,
                    __limit: ACTIVITIES_PER_PAGE,
                },
		    })

            const data = await resCollections.data.data.results;
    
            setQuery({ ...query, page: query.page + 1 });
        
            if (data.length < ACTIVITIES_PER_PAGE) setHasMore(false);
            const newData = [...activities, ...data]
            setactivities(newData);
        } catch (error) {
          console.log(error);
        }

    };

    const hitFilter = async (index) => {
        let tmpdata = dataFilter;
        tmpdata[index].state = !dataFilter[index].state;
        setdataFilter(tmpdata)
        getActivities(tmpdata)
    }

    const clearFilter = () => {
        let tmpdata = dataFilter;
        for (let i = 0; i < tmpdata.length; i++) {
            tmpdata[i].state = false
        }
        setdataFilter(tmpdata)
        getActivities();
    }

    const getActivities = async (tmpdata) => {
        setloading(true);
        let tmpfilters = []
        let tmpfilterdata;
        if(tmpdata)
            tmpfilterdata = tmpdata;
        else
            tmpfilterdata = dataFilter;

        for (let i = 0; i < tmpfilterdata.length; i++) {
            if(tmpfilterdata[i].state)
                tmpfilters.push(unParseFilter(tmpfilterdata[i].name))
        }
        setQuery({page: 0, params: { filter:tmpfilters.join('||') }})
        let initdata = await axios.get(`${process.env.REACT_APP_API_URL}/activities`, {
            params: {
                filter: tmpfilters.join('||'),
                datefilter: parseDateFilter1(radioValue),
                nft_contract_id: collectionId,
                __skip: 0 * ACTIVITIES_PER_PAGE,
                __limit: ACTIVITIES_PER_PAGE,
            },
        })
        if(initdata?.data.data != null){
            setactivities(initdata.data.data.results);
            if (initdata.data.data.results.length >= ACTIVITIES_PER_PAGE) setHasMore(true);
        }
        setloading(false);
    }
    

    const getCollectionChart = async () => {
        try {
            const resCollectionStat = await axios.get(`${process.env.REACT_APP_API_URL}/collection-chart`, {
                params: {
                    nft_contract_id: collectionId,
                    deadline: parseDateFilter1(radioValue)
                },
		    })
            if(resCollectionStat.data.data){
                let data = makeChartData(makeRelayData(resCollectionStat.data.data));
                setoptions(data[1])
                setseries(data[0])
            }

        } catch (error) {
            console.log(error);
        }
    };

    const makeRelayData = (data) => {
        if(data?.length > 0){
            let tmpdata = [], tmpdata1 = data;
            let Today = new Date();
            let lastDay = new Date(Date.parse(tmpdata1[0].issued_at + 'T' + Today.toISOString().split('T')[1]));
            if(lastDay.getDay() == Today.getDay())
                console.log('the same date');
            else{
                let difference = (Today - lastDay)/1000/60/60/24;
                for (let i = 0; i < difference; i++) {
                    let newdate = new Date(new Date() - 86400000*(i))
                    let newdata = {
                        ...tmpdata1[0],
                        issued_at: newdate.toISOString().split('T')[0]
                    }
                    tmpdata.push(newdata)
                }
            }
            let real = tmpdata.concat(tmpdata1);
            return real;
        }else{
            return[[],staticChartOption];
        }
    }

    const makeChartData = (realdata) => {
        let tmpseries;
        let tmpoptions = staticChartOption;
        let labels = []
        let series1 = [], series2 = [];

        for (let i = 0; i < realdata.length; i++) {
            const element = realdata[i];
            labels.push(element.issued_at)
            series1.push(prettyBalance(element.floor_price))
            series2.push(prettyBalance(element.total_volumn))
        }
        tmpoptions.labels = labels.reverse();
        tmpseries = [{
            name: 'Price',
            type: 'area',
            data: series1.reverse(),
        },
        {
            name: 'Volume',
            type: 'column',
            data: series2.reverse(),
        }]
        return [tmpseries, tmpoptions]
    }

    useEffect( () => {
        getCollectionChart();
        getActivities();
    },[radioValue]);

    const radios = [
        { name: '7 days', value: '1' },
        { name: '30 days', value: '2' },
        { name: '90 days', value: '3' },
        { name: '1 year', value: '4' },
        { name: 'all time', value: '5' },
    ];

    const [dataFilter, setdataFilter] = useState(
        [
            {
                icon: 'icon-fl-sort-filled',
                name: 'Listings',
                state: false
            },
            {
                icon: 'icon-fl-discount',
                name: 'Sales',
                state: false
            },
            {
                icon: 'icon-fl-logout',
                name: 'Transfers',
                state: false
            },
            {
                icon: 'icon-fl-credit-card',
                name: 'Offers',
                state: false
            },
        ]
    )

    const [series, setseries] = useState([])
    
    const [options, setoptions] = useState({})

    return (
    <div className='m-5 p-5'>
        <div className='themesflat-container'>
            <Chart options={options} series={series} type='line' height={450} />
            <ButtonGroup className='m-5'>
                {radios.map((radio, idx) => (
                <ToggleButton
                    key={idx}
                    id={`radio-${idx}`}
                    type="radio"
                    variant={'outline-primary'}
                    name="radio"
                    value={radio.value}
                    checked={radioValue === radio.value}
                    onChange={(e) => setRadioValue(e.currentTarget.value)}
                    className='p-3'
                >
                    <big>{radio.name}</big>
                </ToggleButton>
                ))}
            </ButtonGroup>
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-lg-9 col-md-9 col-12">
                            <div className="table-ranking">
                                <div className="flex th-title px-3" style={{ width: '90%' }}>
                                    <div className="column1">
                                        <h3>Items</h3>
                                    </div>
                                    <div className="column">
                                        <h3>Price</h3>
                                    </div>
                                    <div className="column">
                                    <h3>From</h3>
                                    </div>
                                    <div className="column">
                                    <h3>To</h3>
                                    </div>
                                    <div className="column">
                                    <h3>Time</h3>
                                    </div>
                                    <div className="column">
                                    <h3>Type</h3>
                                    </div>
                                </div>
                                <div style={{overflowY: 'hidden'}}>
                                    {loading && (
                                        <Spinner animation="border" role="status" variant="primary" style={{width:"100px", height:"100px", borderWidth: "thick", position:"relative", left: "50%"}}>
                                            <span className="visually-hidden">Loading...</span>
                                        </Spinner>
                                    )}
                                    {!loading && (
                                    <InfiniteScroll
                                        dataLength={activities.length}
                                        next={() => fetchMoreActivities()}
                                        hasMore={hasMore}
                                        loader={
                                            <Spinner animation="border" role="status" variant="primary" style={{width:"40px", height:"40px", position:"relative", left: "50%"}}>
                                                <span className="visually-hidden">Loading...</span>
                                            </Spinner>
                                        }
                                        className="infinite-scroll-grid flex pt-5"
                                        scrollThreshold={0.9}
                                        style={{ height:'auto', overflow: 'hidden'}}
                                    >
                                        {
                                            activities.map((item,index) => (
                                                <div key={index} className="fl-item2 px-2" style={{ width: '95%' }}>
                                                    <div className="item flex">
                                                        <div className="infor-item flex column1">
                                                            <div className="media d-flex">
                                                                <img className="rounded" style={{ objectFit: 'cover' }} src={item?.tokens[0]?.media_url? item.tokens[0].media_url : defaultPhotoUrl} alt="Axies" />
                                                            </div>
                                                            <div className="content-collection align-self-center px-4">
                                                                <h5 className="title"><Link to={`/collection/${item?.contract_id}`}>{prettyTruncate(item?.contract_id, 14, 'address')}</Link></h5>
                                                                <h5 className="title"><Link to={`/collection/${item?.contract_id}/${item?.token_id}`}>{prettyTruncate(item?.tokens[0]?.metadata.title||'', 16) }</Link></h5>
                                                            </div>
                                                        </div>
                                                        <div className="column">
                                                            <span>{prettyBalance(item?.price||'')} </span>
                                                        </div>
                                                        <div className="column td2">
                                                            <span><Link to={`/users/${item?.from}`} className='text-success'>{ prettyTruncate(item?.from, 10, 'address') }</Link></span>
                                                        </div>
                                                        <div className="column td3">
                                                            <span><Link to={`/users/${item?.to}`} className='text-danger'>{ prettyTruncate(item?.to, 10, 'address') }</Link></span>
                                                        </div>
                                                        <div className="column td4">
                                                            <span><small>{parseTimeForFilter(item?.issued_at) }</small></span>
                                                        </div>
                                                        <div className="column td5">
                                                            <span>&nbsp;&nbsp;&nbsp;&nbsp;{ parseFilter(item?.type) }</span>
                                                        </div>
                                                    </div>
                                                </div>
                                            ))
                                        }
                                    </InfiniteScroll>
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-3 col-md-3 col-12">
                            <div id="side-bar" className="side-bar style-2">
                                <div className="widget widget-filter style-2 mgbt-0">
                                    <h3 className="title-widget">Filter</h3>
                                    <ul className="box-check">
                                        {
                                            dataFilter.map((item,index) => (
                                                !item.state ?
                                                <li key={index} onClick={() => hitFilter(index)}><a className="box-widget-filter" style={{ cursor: 'pointer'}}><i className={item.icon}></i>{item.name}</a></li>
                                                :
                                                <li key={index} onClick={() => hitFilter(index)}><a className="box-widget-filter active" style={{ cursor: 'pointer'}}><i className={item.icon}></i>{item.name}</a></li>
                                            ))
                                        }
                                    </ul>
                                    <a onClick={clearFilter} className="clear-check btn-filter style-2" >
                                        Clear All Filters
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
        </div>
    </div>
    );
}

export default CollectionActivity;
