import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom'
import { Modal } from "react-bootstrap";
import {prettyBalance} from "../../utils/common"
import near from '../../lib/near'

const AcceptOfferModal = (props) => {
    const [offerData, setOfferData ] = useState(null);

    useEffect(() => {
        if(props.contract_id=="" || props.token_id=="" || props.buyer_id=="") {
            return
        }
        const getOffer = async () => {
            try {
                const offerDataContract = await near.viewFunction({
                    methodName: 'get_offer',
                    contractId: process.env.REACT_APP_MARKET_CONTRACT,
                    args: {
                        nft_contract_id: props.contract_id,
                        token_id: props.token_id,
                        buyer_id: props.buyer_id
                    }
                })
                setOfferData(offerDataContract)
            } catch(e) {
                  console.log(e)
                  setOfferData(null);
            }
        }

        getOffer()
    }, [props.contract_id, props.token_id, props.buyer_id])

    return (

          <Modal
              show={props.show}
              onHide={props.onHide}
              centered
          >
          <Modal.Header closeButton></Modal.Header>

          <div className="modal-body space-y-20 pd-40">
              <h3>Accept an Offer</h3>
              <p className="text-center"><span className="price color-popup">{props.title} </span>
              </p>
          <div className="d-flex justify-content-between">
                  <p> From:</p>
                  <p className="text-right price color-popup"> {offerData?.buyer_id || props.buyer_id}</p>
              </div>
          <div className="d-flex justify-content-between">
                  <p> Price:</p>
                  <p className="text-right price color-popup"> {prettyBalance(offerData?.price || "0")}  </p>
              </div>
              <button className="btn btn-primary" onClick={()=>props.handleAcceptOffer(offerData?.price || "0")} data-toggle="modal" data-target="#popup_bid_success" data-dismiss="modal" aria-label="Close">Confirm</button>
          </div>
          </Modal>
        
    );
};

export default AcceptOfferModal;
