import React, { useEffect, useState }  from 'react';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import LiveAuction from '../components/layouts/home-4/LiveAuction';
import TopSeller from '../components/layouts/authors/TopSeller';
import SliderStyle5 from '../components/slider/SliderStyle5';
import PopularCollection from '../components/layouts/home-7/PopularCollection';
import axios from 'axios'

const Home07 = () => {
    const [userdata, setuserdata] = useState([]);

    //Init data
    useEffect( async () => {
        let initdata = await axios.get(`${process.env.REACT_APP_API_URL}/getUsers`, {
            params: {
            },
        })
        if(initdata?.data.data != null){
            setuserdata(initdata.data.data);
        }
    },[]);


    return (
        <div className='home-7'>
            <Header />
            <SliderStyle5 />
            <PopularCollection />
            <LiveAuction />
            <TopSeller data={userdata} />
            <Footer />
        </div>
    );
}

export default Home07;
