import React , { useEffect, useState } from 'react';
import { Accordion } from 'react-bootstrap-accordion'
import { Scrollbar } from "react-scrollbars-custom";
import { Form } from "react-bootstrap"
import { calcTimeDelta } from 'react-countdown';

const SideBar = props => {
    const data = props.data;
    const filters = []

    const [dataCate, SetDataCate] = useState(
        []
    )
    const visible = props.visible;
    const setvisible = props.setvisible;


    const [isVisible, setIsVisible] = useState(false);
    
    const scrollToTop = () => {
      window.scrollTo({
        top: 0,
        behavior: "smooth"
      });
    };

    useEffect(() => {
        let attributes = []
        Object.entries(data).forEach(([key, value]) => {
            let contents = []
            Object.entries(value).forEach(([key1, value1]) => {
                contents.push({field: `${key1} (${value1.count})`, checked: ''})
            });
            attributes.push({title: key, content: contents})
        });
        SetDataCate([...filters, ...attributes])
            const toggleVisibility = () => {
            if (window.pageYOffset > 500) {
            setIsVisible(true);
            } else {
            setIsVisible(false);
            }
        };
    
        window.addEventListener("scroll", toggleVisibility);
    
        return () => window.removeEventListener("scroll", toggleVisibility);
    }, [props]);

  return (
    <div id="side-bar" className={`side-bar style-3 item mb-0 mt-3 pt-4 ${visible? '': 'filter_hide' }`}>
        <div className="widget widget-filter style-1 mgbt-0">
            <div className="header-widget-filter">
                <h4 className="title-widget"><i className="fa fa-filter" aria-hidden="true"></i> &nbsp;Filters</h4>
                <big className='px-3 filterclosebtn' style={{ cursor: 'pointer'}} onClick={ () => setvisible(false)} ><i className="fa fa-times" aria-hidden="true" style={{ fontSize: '20px' }}></i></big>
            </div>
        </div>     
        <div className="divider"></div>
        <Scrollbar style={{height: 'calc(100vh - 400px)' }}>
            <div>
                <div className="wrap-category">
                    <div className="widget widget-category boder-bt">
                        <form>
                            <label className='px-3 py-1'>
                                <span><big>For Sale</big></span>
                                <span className="pst-re">
                                    <input type="checkbox" onClick={(ev) => props.setsale(ev.target.checked)} />
                                    <span className="btn-checkbox"></span>
                                </span>
                            </label><br/>
                            <label className='px-3'>
                                <span><big>Most Viewed</big></span>
                                <span className="pst-re">
                                    <input type="checkbox" onClick={(ev) => props.setview(ev.target.checked)} />
                                    <span className="btn-checkbox"></span>
                                </span>
                            </label><br/>
                            <label className='px-3'>
                                <span><big>Most Liked</big></span>
                                <span className="pst-re">
                                    <input type="checkbox" onClick={(ev) => props.setlike(ev.target.checked)} />
                                    <span className="btn-checkbox"></span>
                                </span>
                            </label><br/>
                        </form>
                    </div>
                    <p className='mb-3 mx-4'>Attributes</p>
                    {
                        dataCate.map((item,index)=> (
                            <div key={index} className="widget widget-category boder-bt">
                                <Accordion title={item.title} show={false}>
                                    <form>
                                        {
                                            item.content.map((itemm , index1) => (
                                                <div key={index1}>
                                                    <label>
                                                        <span><big>{itemm.field}</big></span>
                                                        <span className="pst-re">
                                                            <input type="checkbox" onClick={(ev) => props.clickFilter(index,index1, ev.target.checked)} checked={props.collectionFilter[index][index1] == 0 ? false: true} readOnly/>
                                                            {/* <input type="checkbox" defaultChecked={itemm.checked}  onClick={(ev) => props.clickFilter(index,index1, ev.target.checked)} checked={props.collectionFilter[index][index1] == 0 ? false: true}/> */}
                                                            <span className="btn-checkbox"></span>
                                                        </span>
                                                    </label><br/>
                                                </div>
                                            ))
                                        }                                            
                                    </form>
                                </Accordion>
                            </div>
                        ))
                    }
                </div>
            </div>
        </Scrollbar>
        <div className='filtersetbtn'>
            <div className='row px-3 py-5 w-100'>
                <div className='col-6'>
                    <button className='w-100' onClick={ () => props.clear() }><big>Clear</big></button>
                </div>
                <div className='col-6'>
                    <button className='w-100' onClick={ () => props.done() }><big>Done</big></button>
                </div>
            </div>
        </div>
    </div>
  );
}

export default SideBar;