import React, { useEffect, useState, useCallback } from 'react';
import { Link, useParams } from 'react-router-dom'
import axios from 'axios';
import BN from 'bn.js';
import {prettyTruncate, prettyBalance, parseFilter} from "../utils/common"
import useStore from '../lib/store'

const ItemHistory = ({contract_id, token_id}) => {
    const [activity, setActivity] = useState([])
    const store = useStore()
    const getActivities = useCallback(async () => {
        if(!contract_id || !token_id) return;
        try {
            const resActivity = await axios.get(`${process.env.REACT_APP_API_URL}/activities`, {
                params: {
                    nft_contract_id: contract_id,
                    token_id: token_id
                },
		    })

            setActivity(resActivity.data.data.results);
    
        } catch (error) {
            console.log(error);
            setActivity([]);
        }
    },[contract_id, token_id]);

    useEffect(() => {
        if(!contract_id || !token_id) return;
        getActivities();
    }, [contract_id, token_id]);

    return (
        <ul className="bid-history-list">
                <li className='py-0'>
                    <div className="sc-author-box-title">
                        <div className='col-3 author-infor'><h6>From</h6></div>
                        <div className='col-3 author-infor'><h6>To</h6></div>
                        <div className='col-4 author-infor'><h6>Type/Time</h6></div>
                        <div className='col-2 author-infor'><h6>Price</h6></div>
                    </div>
                </li>
        {
            activity.length>0&&activity.map((item, index) => (
                <li key={index} item={item}>
                    <div className="sc-author-box style-2 content row">
                        <div className='col-3 author-infor'>
                            <div className='name'>
                                <h6><Link to={`/${item.from}`}>{prettyTruncate(item.from, 16, 'address')} </Link></h6>
                            </div>
                        </div>
                        <div className='col-3 author-infor'>
                            <div className='name'>
                                {item.to?(<h6><Link to={`/${item.to}`}>{prettyTruncate(item.to, 16, 'address')} </Link></h6>):(<></>)}
                            </div>
                        </div>
                        <div className='col-4 author-infor'>
                            <div className='name'>
                                <h6> {parseFilter(item.type)}</h6>
                            </div>
                            <span className="time">{item.issued_at}</span>
                        </div>
                        <div className='col-2 price'>
                            <h5>{item.price?prettyBalance(item.price)+" NEAR":""}</h5>
                            <span>{item.price?`= $${prettyBalance(new BN(item.price) * store.nearUsdPrice, 24, 4)}`:""}</span>
                        </div>
                        
                    </div>
                </li>
            ))
        }
        </ul>
    );
}

export default ItemHistory;
