import React, { useState, useEffect } from 'react';
import { Link, useParams, useNavigate } from 'react-router-dom';
import { useWalletSelector } from '../hooks/WalletSelectorContext'
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import axios from 'axios'
import { BreadcrumbItem, Form, InputGroup } from 'react-bootstrap'
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import defaultBannerUrl from '../assets/images/backgroup-secsion/option1_bg_profile.jpg';
import defaultPhotoUrl from '../assets/images/avatar/avt-2.jpg';

var FormData = require('form-data');

const EditCollection = () => {
    const { collectionId } = useParams();
    const navigate = useNavigate();
    const { accountId, setCommonModal } = useWalletSelector()
    //user collection field
    const [banimg_url, setbanimg_url] = useState("");
    const [bgimg_url, setbgimg_url] = useState("");
    const [name, setname] = useState("");
    const [website_url, setwebsite_url] = useState("");
    const [description, setdescription] = useState("");
    const [twitter_url, settwitter_url] = useState("");
    const [discord_url, setdiscord_url] = useState("");
    const [instagram, setinstagram] = useState("");

    //update user collection field button function
    const updateProfile = async () => {
        let status = false;
        status = checkDataField([name, description, banimg_url, bgimg_url ])
        if (status) 
        {
            let updateData = { contract_id: collectionId, name, description, banimg_url, bgimg_url, website_url, twitter_url, discord_url, instagram }
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/updateCollection`, {
                params: {
                    ...updateData,
                },
            })
            toast('Successfully Updated!', {
                position: toast.POSITION.BOTTOM_RIGHT,
                className: 'foo-bar'
            })
        }
    }

    const uploadbanimg_url = (ev) => {
        console.log('upload new banimg_url')
        const file = ev.target.files[0];
        const url = `https://api.pinata.cloud/pinning/pinFileToIPFS`;
        const formData = new FormData();
        formData.append("file",file);
        return axios.post(url,formData,{
            headers:{
                'Content-Type': `multipart/form-data; boundary= ${formData._boundary}`,
                'pinata_api_key':'fe3060199e1632e4d52a',
                'pinata_secret_api_key':'1f4a05527f4e641e6fe037affea5fb577196dd8c34f7db093c0d27102f3e6ae7',
            }
        }).then(function (response) {
            setbanimg_url("https://nearnaut.mypinata.cloud/ipfs/" + response.data.IpfsHash)
            console.log('banimg_url', "https://nearnaut.mypinata.cloud/ipfs/" + response.data.IpfsHash)
        }).catch(function (error) {
            console.log('cant upload banimg_url',error)
        });
    }

    const uploadbanner = (ev) => {
        const file = ev.target.files[0];
        const url = `https://api.pinata.cloud/pinning/pinFileToIPFS`;
        const formData = new FormData();
        formData.append("file",file);
        return axios.post(url,formData,{
            headers:{
                'Content-Type': `multipart/form-data; boundary= ${formData._boundary}`,
                'pinata_api_key':'fe3060199e1632e4d52a',
                'pinata_secret_api_key':'1f4a05527f4e641e6fe037affea5fb577196dd8c34f7db093c0d27102f3e6ae7',
            }
        }).then(function (response) {
            setbgimg_url("https://nearnaut.mypinata.cloud/ipfs/" + response.data.IpfsHash)
            console.log('banner', "https://nearnaut.mypinata.cloud/ipfs/" + response.data.IpfsHash)
        }).catch(function (error) {
            console.log('cant upload banner',error)
        });
    }

    const deletebanimg_url = () => {
        setbanimg_url("")
    }

    const checkDataField = (datalist) => {
        for (let index = 0; index < datalist.length; index++) {
            const element = datalist[index];
            if(element == ""){
                return false;
            }
        }
        return true;
    }
    
    //Init data
    useEffect( async () => {
        if(!accountId) {
            navigate("/");
            return
        }
        let initdata = await axios.get(`${process.env.REACT_APP_API_URL}/collections`, {
            params: {
                collection_id: collectionId,
            },
        })
        if(initdata?.data.data != null){
            //get information of contract_id
            const maindata = initdata.data.data.results[0];
            setname(maindata.name);
            setdescription(maindata.description);
            setbanimg_url(maindata.banimg_url);
            setbgimg_url(maindata.bgimg_url);
            setwebsite_url(maindata.website_url || "");
            setdiscord_url(maindata.discord_url || "");
            settwitter_url(maindata.twitter_url || "");
            setinstagram(maindata.instagram || "");
        }
    },[accountId]);

    return (
        <div>
            <Header />
            <section className="flat-title-page inner">
                <div className="overlay"></div>
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="page-title-heading mg-bt-12">
                                <h1 className="heading text-center">Edit Collection</h1>
                            </div>
                        </div>
                    </div>
                </div>                    
            </section>
            <div className="tf-create-item tf-section">
                <div className="themesflat-container">
                    <div className="row">
                         <div className="col-xl-3 col-lg-4 col-md-12 col-12">
                            <div className="sc-card-profile text-center">
                                <div className="card-media">
                                    {banimg_url=="" ? 
                                    <img id="profileimg" src={defaultPhotoUrl} alt="Axies" />
                                    :
                                    <img id="profileimg" src={banimg_url} alt="Axies" style={{ margin: '20px 0px', borderRadius: '10px'}}/>
                                    }
                                </div>
                                <div id="upload-profile">
                                    <Link to="#" className="btn-upload">Upload New Banner Image</Link>
                                    <input id="tf-upload-img" type="file" name="profile" onChange={(ev) => uploadbanimg_url(ev)}/>
                                </div>
                                <button className="btn-upload style2 border-0" onClick={deletebanimg_url}>
                                    Delete
                                </button>
                            </div>
                         </div>
                         <div className="col-xl-9 col-lg-8 col-md-12 col-12">
                            <div className="form-upload-profile">
                                <h4 className="title-create-item">Choice your Cover image</h4>
                                <div className="option-profile clearfix row">
                                    <div className="sc-card-profile text-center col-12 col-sm-3">
                                        <div id="upload-profile">
                                            <Link to="" className="btn-upload">Upload Background</Link>
                                            <input id="tf-upload-img" type="file" name="profile" onChange={(ev) => uploadbanner(ev)}/>
                                        </div>
                                        <button className="btn-upload style2 border-0" onClick={ () => setbgimg_url("")} >
                                            Delete
                                        </button>
                                    </div>
                                    <div className="image col-12 col-sm-8">
                                        <img src={bgimg_url != ""? bgimg_url : defaultBannerUrl} alt="Axies" />
                                    </div>
                                </div>
                                <div className="form-profile">
                                    <div className="form-infor-profile">
                                        <div className="info-account">
                                            <h4 className="title-create-item">Collection info</h4>
                                            <InputGroup hasValidation className='mb-5'>
                                                <InputGroup.Text className='col-3'><p style={{ fontSize: '1.5rem'}} className='m-auto'>Name</p></InputGroup.Text>
                                                <Form.Control type="text" placeholder="Place the collection name" value={name} onChange={(ev) => setname(ev.target.value)} required isInvalid={name=="" ? true : false} />
                                                <Form.Control.Feedback type="invalid">
                                                    <div style={{ fontSize: '1.3rem'}}>Please type name.</div>
                                                </Form.Control.Feedback>
                                            </InputGroup>
                                            <InputGroup hasValidation className='mb-5'>
                                                <InputGroup.Text className='col-3'><p style={{ fontSize: '1.5rem'}} className='m-auto'>Description</p></InputGroup.Text>
                                                <Form.Control as="textarea" tabIndex="4" rows="5" value={description} onChange={(ev) => setdescription(ev.target.value)} required isInvalid={description =="" ? true : false} />
                                                <Form.Control.Feedback type="invalid">
                                                    <div style={{ fontSize: '1.3rem'}}>Please type your description information.</div>
                                                </Form.Control.Feedback>
                                            </InputGroup>
                                        </div>
                                        <div className="info-social">
                                            <h4 className="title-create-item">Collection Social Info</h4>
                                            <InputGroup hasValidation className='mb-5'>
                                                <InputGroup.Text className='col-3'><div style={{ fontSize: '1.5rem' }} className='m-auto'>Twitter</div></InputGroup.Text>
                                                <Form.Control type="text" placeholder="Twitter URL" value={twitter_url} onChange={(ev) => settwitter_url(ev.target.value)}/>
                                                <Form.Control.Feedback type="invalid">
                                                    <div style={{ fontSize: '1.3rem'}}>Please type Twitter URL.</div>
                                                </Form.Control.Feedback>
                                            </InputGroup>
                                            <InputGroup hasValidation className='mb-5'>
                                                <InputGroup.Text className='col-3'><div style={{ fontSize: '1.5rem' }} className='m-auto'>Discord</div></InputGroup.Text>
                                                <Form.Control type="text" placeholder="Discord URL" value={discord_url} onChange={(ev) => setdiscord_url(ev.target.value)}/>
                                            </InputGroup>
                                            <InputGroup hasValidation className='mb-5'>
                                                <InputGroup.Text className='col-3'><div style={{ fontSize: '1.5rem' }} className='m-auto'>Instagram</div></InputGroup.Text>
                                                <Form.Control type="text" placeholder="Instagram URL" value={instagram} onChange={(ev) => setinstagram(ev.target.value)}/>
                                            </InputGroup>
                                            <InputGroup hasValidation className='mb-5'>
                                                <InputGroup.Text className='col-3'><p style={{ fontSize: '1.5rem'}} className='m-auto'>Website</p></InputGroup.Text>
                                                <Form.Control type="text" placeholder="nauts.near.com/" value={website_url} onChange={(ev) => setwebsite_url(ev.target.value)} />
                                                <Form.Control.Feedback type="invalid">
                                                    <div style={{ fontSize: '1.3rem'}}>Please type website url.</div>
                                                </Form.Control.Feedback>
                                            </InputGroup>
                                        </div> 
                                    </div>
                                    <button className="tf-button-submit mg-t-15" onClick={ updateProfile }>
                                        Update Collection
                                    </button>
                                </div>
                            </div>
                         </div>
                    </div>
                </div>
            </div>
            <Footer />
        </div>
    );
}

export default EditCollection;
