import React , { useState, useEffect } from 'react';
import { Link } from 'react-router-dom'
import axios from 'axios'

import imgTop1 from '../../../assets/images/box-item/collection-item-17.jpg'
import img1Bottom1 from '../../../assets/images/box-item/collection-item-16.jpg'
import imgAuthor1 from '../../../assets/images/avatar/avt-7.jpg'

const PopularCollection = () => {
    const [userdata, setuserdata] = useState([]);

    const fetchrandomusers = async () => {
        let params = {
            is_popular: true,
            __skip: 0,
            __limit: 6,
        }
        try {
            const initdata = await axios.get(`${process.env.REACT_APP_API_URL}/collections`, {
                params
            })
            if(initdata?.data.data != null){
                setuserdata(initdata.data.data.results);
                // console.log('popular collections data', initdata.data.data.results)
            }
        } catch (error) {
            console.log(error);
            setuserdata([]);
        }
    }

    useEffect( async () => {
        await fetchrandomusers();
    },[]);

    return (
        <section className="tf-section live-auctions style4 home4 live-auctions-style7">
            <div className="themesflat-container">
                <div className="row">
                    <div className="col-box-12">
                        <div className="heading-live-auctions">
                            <h2 className="tf-title pb-40 text-left">
                                Popular Collections</h2>
                            <Link to="/collections" className="exp style2 mg-t-23">MORE</Link>
                        </div>
                    </div>
                    {   
                        userdata.length > 1 && userdata.map((item,index) =>(
                            <div key={index} className="fl-collection fl-item3 col-xl-3 col-md-4">
                                <Link to={`/collection/${item.contract_id}`}>
                                    <div className="sc-card-collection style-2 sc-card-style7">
                                        <div className="card-media-h7">
                                            <figure style={{ position: 'relative'}}>
                                                <img src={item.bgimg_url ? item.bgimg_url : imgTop1} alt="Axies" style={{ height: '200px', objectFit: 'cover' }} />
                                                <figcaption style={{ position: 'absolute', top: '8px', right: '16px', zIndex: 1}}>
                                                    {
                                                        item.is_trending &&
                                                        <span style={{ fontSize: '40px', color: '#58b4e3' }}><i className="far fa-chart-bar"></i></span>
                                                    }
                                                </figcaption>
                                            </figure>
                                            {/* <i class="far fa-chart-bar"></i> */}
                                            {/* <i className="fas fa-chart-line"></i> */}
                                        </div>
                                        <div className="card-media-h7 style2">
                                            {/* {console.log('item.tokens',item.tokens)} */}
                                            <img src={item.tokens[0]?.media_url || (item.tokens[0]?.metadata?.image ? item.tokens[0].metadata.image?.replace("ipfs://", "https://cloudflare-ipfs.com/ipfs/") : img1Bottom1)} alt="Axies" />
                                            <img src={item.tokens[1]?.media_url || (item.tokens[1]?.metadata?.image ? item.tokens[1].metadata.image?.replace("ipfs://", "https://cloudflare-ipfs.com/ipfs/") : img1Bottom1)} alt="Axies" />
                                            <img src={item.tokens[2]?.media_url || (item.tokens[2]?.metadata?.image ? item.tokens[2].metadata.image?.replace("ipfs://", "https://cloudflare-ipfs.com/ipfs/") : img1Bottom1)} alt="Axies" />
                                        </div>
                                        <div className="card-bottom">
                                            <div className="author">
                                                <div className="content">
                                                    <h5>{item.title}</h5>
                                                    <div className="infor">
                                                        <p>Collection</p>
                                                        <p className="name">{item.name}</p>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="author">
                                                <div className="content">
                                                    <h5>{item.title}</h5>
                                                    <div className="infor">
                                                        <p>Items</p>
                                                        <p className="name">{item.total_count}</p>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="sc-author-box style-2">
                                            <div className="author-avatar">
                                                <img src={item.banimg_url ? item.banimg_url : imgAuthor1} alt="Axies" className="avatar" />
                                                {
                                                    item.verified &&
                                                    <div className="badge"><i className="ripple"></i></div>
                                                }
                                            </div>
                                        </div>
                                    </div>
                                </Link>
                            </div>
                        ))
                    }
                </div>
            </div>
    </section>
    );
}


export default PopularCollection;
