import React, { useState, useEffect, useCallback } from 'react';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import { Link, useParams } from 'react-router-dom';
import Countdown from "react-countdown";
import axios from 'axios';
import BN from 'bn.js';
import { GAS_FEE, GAS_FEE_150, GAS_FEE_200, STORAGE_ADD_MARKET_FEE, STORAGE_APPROVE_FEE } from '../config/constants'
import {amountInYocto, parseYoctoNear, getAmount} from '../lib/near.js'
import { Tab, Tabs, TabList, TabPanel  } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import { useWalletSelector } from '../hooks/WalletSelectorContext'
import near from '../lib/near'
import { Scrollbar } from "react-scrollbars-custom";
import img1 from '../assets/images/avatar/avatar.jpg'
import ItemHistory from './ItemHistory';
import OfferModal from '../components/Modal/OfferModal';
import ListModal from '../components/Modal/ListModal';
import DelistModal from '../components/Modal/DelistModal';
import BuyModal from '../components/Modal/BuyModal';
import TransferModal from '../components/Modal/TransferModal';
import AcceptOfferModal from '../components/Modal/AcceptOfferModal';
import CancelOfferModal from '../components/Modal/CancelOfferModal';
import {prettyTruncate, prettyBalance} from "../utils/common"
import useStore from '../lib/store'
import { ReactComponent as VerifiedSVG } from "../assets/images/svg/newverified1.svg";
import { ReactComponent as VerifiedSVG1 } from "../assets/images/svg/verified1.svg";

const ItemDetails01 = () => {
    const {collectionId, tokenId} = useParams();
    const [token, setToken] = useState(null);
    const { accountId, setCommonModal, modal, selector } = useWalletSelector()
    const store = useStore()
    console.log('accountId',accountId)
    const [selectedBuyer, setSelecteBuyer] = useState("");
    const [royalty, setRoyalty] = useState("");
    const [buymodalShow, setBuyModalShow] = useState(false);
    const [offermodalShow, setOfferModalShow] = useState(false);
    const [acceptoffermodalShow, setAcceptOfferModalShow] = useState(false);
    const [unoffermodalShow, setUnOfferModalShow] = useState(false);
    const [listmodalShow, setListModalShow] = useState(false);
    const [delistmodalShow, setDelistModalShow] = useState(false);
    const [transfermodalShow, setTransferModalShow] = useState(false);
    const [is_favor, setfavor] = useState(false);
    const [viewcount, setviewcount] = useState(0);
    const [likecount, setlikecount] = useState(0);

    const clickFavor = async () => {
        if(accountId){
            if(is_favor)
                    setlikecount(parseInt(likecount) - 1);
                else
                    setlikecount(parseInt(likecount) + 1);
            setfavor(!is_favor);
            
            let params = { accountId, collectionId, tokenId, is_favor: !is_favor }
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/like`, {
                params
            })
            if(res.data.data == 'success'){
                
            }
        }
    }

    const view = async () => {
        if(accountId){
            let params = { accountId, collectionId, tokenId }
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/isview`, {
                params
            })
        }
    }

    const is_like = async () => {
        if(accountId){
            let params = { accountId, collectionId, tokenId }
            const res = await axios.get(`${process.env.REACT_APP_API_URL}/islike`, {
                params
            })
            console.log('is likes',res.data.data.results)
            if(res.data.data.results){
                setfavor(res.data.data.results.is_favor);
            }else{
                setfavor(false);
            }
        }
    }

    const getRoyalty = useCallback(async () => {
        if(!token) return
        try {
            const royaltyDataContract = await near.viewFunction({
                methodName: 'nft_payout',
                contractId: token.contract_id,
                args: {
                    token_id: token.token_id,
                    balance: '100',
                    max_len_payout: 7
                }
            })
            const payout = royaltyDataContract.payout;
            const params = (100 - parseFloat(payout[token.owner_id]))//.toFixed(2).toPrecision()
            console.log('token',payout)
            setRoyalty(params || 3)
        } catch(e) {
              console.log(e)
              setRoyalty("");
        }
    },[token])

    const getToken = useCallback(async (collection_id, token_id) => {
        try {
            const resToken = await axios.get(`${process.env.REACT_APP_API_URL}/token`, {
                params: {
                    collection_id: collection_id,
                    token_id: token_id,
                },
		    })
            if( resToken.data.data ){
                let res = resToken.data.data.results;
                setToken(res);
                setlikecount(res.likes ? res.likes : 0);
                setviewcount(res.views ? res.views : 0);
            }
        } catch (error) {
            console.log(error);
            setToken(null);
        }
    
    },[collectionId, tokenId]);

    useEffect(() => {
        if(!token) return
        getRoyalty()
    }, [token]);

    useEffect(() => {
        if(!collectionId||!tokenId) return
        is_like();
        view();
        getToken(collectionId, tokenId)
    }, [collectionId, tokenId]);

    const checkAccount = (e) => {
        e.preventDefault()
        if (!accountId) {
			modal.show()
			return
		}
    }

    const handleBuy = async () => {
        const wallet = await selector.wallet();
        try{
            const txs = []
            const params = {
				token_id: token?.token_id,
                nft_contract_id: token?.contract_id,
                ft_token_id: token?.ft_token_id,
                price: token?.price
			}
            let res

            txs.push(
                {
                    receiverId: process.env.REACT_APP_MARKET_CONTRACT,
                    actions: [
                        {
                        type: "FunctionCall",
                        params: {
                            methodName: "buy",
                            args: params,
                            gas: GAS_FEE_200,
                            deposit: getAmount(token?.price),
                        },
                        },
                    ],
                }
            )
            
            await wallet.signAndSendTransactions({
                transactions: txs
            });

        } catch(e) {
            console.log(e)
        }
    }

    const handleDelist =async () => {
        const wallet = await selector.wallet();
        try{
            const txs = []
            const params = {
				token_id: token?.token_id,
                nft_contract_id: token?.contract_id,
			}
            let res

            txs.push(
                {
                    receiverId: process.env.REACT_APP_MARKET_CONTRACT,
                    actions: [
                        {
                        type: "FunctionCall",
                        params: {
                            methodName: "delete_market_data",
                            args: params,
                            gas: GAS_FEE_200,
                            deposit: '1',
                        },
                        },
                    ],
                }
            )

            txs.push(
                {
                    receiverId: token?.contract_id,
                    actions: [
                        {
                        type: "FunctionCall",
                        params: {
                            methodName: "nft_revoke",
                            args: {
                                token_id: token?.token_id,
                                account_id: process.env.REACT_APP_MARKET_CONTRACT
                            },
                            gas: GAS_FEE_200,
                            deposit: '1',
                        },
                        },
                    ],
                }
            )
            
            await wallet.signAndSendTransactions({
                transactions: txs
            });
            

        } catch(e) {
            console.log(e)
        }
    }

    const handleTransfer =async (account_id) => {
        const wallet = await selector.wallet();
        try{
            const txs = []
            let res
            txs.push(
                {
                    receiverId: token?.contract_id,
                    actions: [
                        {
                        type: "FunctionCall",
                        params: {
                            methodName: "nft_transfer",
                            args: {
                                token_id: token?.token_id,
                                receiver_id: account_id
                            },
                            gas: GAS_FEE_200,
                            deposit: '1',
                        },
                        },
                    ],
                }
            )
            
            await wallet.signAndSendTransactions({
                transactions: txs
            });
            

        } catch(e) {
            console.log(e)
        }
    }

    const hasStorageBalance = async () => {
		try {
			const currentStorage = await near.viewFunction({
				methodName: 'storage_balance_of',
				contractId: process.env.REACT_APP_MARKET_CONTRACT,
				args: { account_id: accountId },
			})

			const supplyPerOwner = await near.viewFunction({
				methodName: 'get_supply_by_owner_id',
				contractId: process.env.REACT_APP_MARKET_CONTRACT,
				args: { account_id: accountId },
			})

            const usedStorage = new BN(parseInt(supplyPerOwner) + 1).mul( new BN(STORAGE_ADD_MARKET_FEE))

			if (new BN(currentStorage).gte(usedStorage)) {
				return true
			}
			return false
		} catch (err) {
			console.log(err)
		}
	}

    const handleOffer = async(balance) => {
        const wallet = await selector.wallet();
        try{
            const hasDepositStorage = await hasStorageBalance()

            const params = {
				nft_contract_id: token?.contract_id,
				token_id: token?.token_id,
				ft_token_id: 'near',
				price: amountInYocto(balance.toString()),
			}
            let res
            if (hasDepositStorage) {
                await wallet.signAndSendTransactions({
                    transactions: [
                      {
                        receiverId: process.env.REACT_APP_MARKET_CONTRACT,
                        actions: [
                          {
                            type: "FunctionCall",
                            params: {
                              methodName: "storage_deposit",
                              args: {},
                              gas: GAS_FEE_150,
                              deposit: amountInYocto(STORAGE_ADD_MARKET_FEE),
                            },
                          },
                        ],
                      },
                      {
                        receiverId: token?.contract_id,
                        actions: [
                          {
                            type: "FunctionCall",
                            params: {
                              methodName: "nft_approve",
                              args: params,
                              gas: GAS_FEE_150,
                              deposit: amountInYocto(balance.toString()),
                            },
                          },
                        ],
                      },
                    ],
                  });
            } else {
                await wallet.signAndSendTransactions({
                    transactions: [
                      {
                        receiverId: token?.contract_id,
                        actions: [
                          {
                            type: "FunctionCall",
                            params: {
                              methodName: "nft_approve",
                              args: params,
                              gas: GAS_FEE_150,
                              deposit: amountInYocto(balance.toString()),
                            },
                          },
                        ],
                      },
                    ],
                  });
            }

        } catch(e) {
            console.log(e)
        }
    }

    const handleCancelOffer = async()=>{
        const wallet = await selector.wallet();
        try{
            const txs = []
            const params = {
				token_id: token?.token_id,
                nft_contract_id: token?.contract_id,
			}
            let res

            txs.push(
                {
                    receiverId: process.env.REACT_APP_MARKET_CONTRACT,
                    actions: [
                        {
                        type: "FunctionCall",
                        params: {
                            methodName: "delete_offer",
                            args: params,
                            gas: GAS_FEE_200,
                            deposit: '1',
                        },
                        },
                    ],
                }
            )
            
            await wallet.signAndSendTransactions({
                transactions: txs
            });
            

        } catch(e) {
            console.log(e)
        }
    }

    const handleAcceptOffer = async(balance)=>{
        const wallet = await selector.wallet();
        try{
            const txs = []
            const params = {
				token_id: token?.token_id,
                account_id: process.env.REACT_APP_MARKET_CONTRACT,
                msg: JSON.stringify({
					market_type: 'accept_offer',
                    buyer_id: selectedBuyer,
					price: amountInYocto(balance.toString())
				}),
			}
            let res

            txs.push(
                {
                    receiverId: token?.contract_id,
                    actions: [
                        {
                        type: "FunctionCall",
                        params: {
                            methodName: "nft_approve",
                            args: params,
                            gas: GAS_FEE_200,
                            deposit: amountInYocto(STORAGE_APPROVE_FEE),
                        },
                        },
                    ],
                }
            )
            
            await wallet.signAndSendTransactions({
                transactions: txs
            });
            

        } catch(e) {
            console.log(e)
        }
    }

    const handleListing = async(balance) => {
        const wallet = await selector.wallet();
        try{
            const hasDepositStorage = await hasStorageBalance()
            const txs = []
            const params = {
				token_id: token?.token_id,
                account_id: process.env.REACT_APP_MARKET_CONTRACT,
                msg: JSON.stringify({
					price: amountInYocto(balance.toString()),
					market_type: 'sale',
					ft_token_id: `near`,
				}),
			}
            let res
            if (hasDepositStorage) {
                txs.push(
                    {
                        receiverId: process.env.REACT_APP_MARKET_CONTRACT,
                        actions: [
                          {
                            type: "FunctionCall",
                            params: {
                              methodName: "storage_deposit",
                              args: {},
                              gas: GAS_FEE_150,
                              deposit: amountInYocto(STORAGE_ADD_MARKET_FEE),
                            },
                          },
                        ],
                      }
                )
            }

            txs.push(
                {
                    receiverId: token?.contract_id,
                    actions: [
                        {
                        type: "FunctionCall",
                        params: {
                            methodName: "nft_approve",
                            args: params,
                            gas: GAS_FEE_200,
                            deposit: amountInYocto(STORAGE_APPROVE_FEE),
                        },
                        },
                    ],
                }
            )
            
            await wallet.signAndSendTransactions({
                transactions: txs
            });
            

        } catch(e) {
            console.log(e)
        }

    }
    


    return (
        <div className='item-details'>
            <Header />
            <section className="flat-title-page inner">
            <div className="overlay"></div>
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="page-title-heading mg-bt-12">
                                <h1 className="heading text-center">{token?.contract_name || "Collection"}
                                {token?.contract_verified == 'true' ? <VerifiedSVG1 style={{ marginTop: '20px' }}></VerifiedSVG1> : <></>}</h1>
                            </div>
                           
                        </div>
                    </div>
                </div>                 
            </section>
            <div className="tf-section tf-item-details">
                <div className="themesflat-container-1">
                    <div className="row">
                        <div className="col-xl-6 col-md-12 px-xl-5 px-md-0">
                            <div className="content-left">
                                <div className="media">
                                    <img className="img-fluid" style={{objectFit:"contain"}} src={token?.media_url||img1} alt="Axies" />
                                </div>
                                <div className="client-infor sc-card-product mt-sm-5 d-none d-xl-flex">
                                    <div className="meta-info">
                                        <div className="author">
                                            <div className="info">
                                                <span style={{fontSize: "1.5rem"}}>Description</span>
                                                <p className='mt-3'>{token?.metadata.description || ""}</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-6 col-md-12">
                            <div className="content-right">
                                <div className="sc-item-details">
                                    <div className="meta-item mb-2 flex-column flex-sm-row">
                                        <div className="left col-0 col-sm-8">
                                            <h4 className="style2 pb-4" style={{overflowWrap:"anywhere"}}>{token?.contract_name || "Collection"}
                                            {token?.contract_verified == 'true' ? <VerifiedSVG></VerifiedSVG> : <></>}</h4>
                                        </div>
                                        <div className="right col-0 col-sm-4 d-flex justify-content-center justify-content-sm-end">
                                            <span className="viewed eye">{viewcount}</span>
                                            <span onClick={ () => clickFavor() } className="liked heart wishlist-button mg-l-8" style={ is_favor ? { color: 'red' } : {}}><span className="number-like">{likecount}</span></span>
                                        </div>
                                    </div>
                                    
                                    <h3 className="style2 mb-5">{token?.metadata.title || "Token Name"}</h3>
                                    
                                    <div className="meta-item mb-2" >
                                        <div className="d-inline">
                                            <h6 className="ps-3">Owned By</h6>
                                            <h6 className="mt-1 px-4 py-2" style={{borderRadius: "40px", backgroundColor: "var(--primary-color5)"}}> <Link to={`/users/${token?.owner_id}`}>{prettyTruncate(token?.owner_id||"",16,`address`)}</Link> </h6>
                                        </div>
                                        <div className="d-inline">
                                            <h6 className="ps-3">Royalty</h6>
                                            <h6 className="mt-1 px-4 py-2" style={{padding: "9px 15px", borderRadius: "40px", backgroundColor: "var(--primary-color5)"}}> {royalty}% </h6>
                                        </div>
                                    </div>
                                    
                                    <div className="client-infor sc-card-product mt-sm-5 d-flex d-xl-none">
                                        <div className="meta-info" style={{width:"100%"}}>
                                            <div className="author">
                                                <div className="info">
                                                    <span style={{fontSize: "1.5rem"}}>Description</span>
                                                    <p className='mt-3'>{token?.metadata.description || ""}</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="meta-item-details style2">
                                        <div className="item meta-price w-100">
                                            {token && token.is_sale?(
                                            <>
                                                <span className="heading">Current Price</span>
                                                <div className="price">
                                                    <div className="price-box">
                                                        <h5> {prettyBalance(token.price)} NEAR</h5>
                                                        <span>= ${prettyBalance(new BN(token.price) * store.nearUsdPrice, 24, 4)}</span>
                                                    </div>
                                                </div>
                                            </>):(
                                                <span className="heading">Not for Sale</span>
                                            )}
                                            
                                        </div>
                                    </div>
                                    <div className='row'>
                                        {token && accountId?(
                                            <>{token.owner_id == accountId ? (
                                                <>  {token.is_sale?(
                                                        <>
                                                            <div className='col-0 col-sm-6'>
                                                                <button className="sc-button loadmore style delist fl-button pri-3 w-100"  onClick={()=>setDelistModalShow(true)}><span>Delist</span></button>
                                                            </div>
                                                        </>
                                                    ):(
                                                        <>
                                                            <div className='col-0 col-sm-6'>
                                                                <button className="sc-button loadmore style list fl-button pri-3 w-100" onClick={()=>setListModalShow(true)}><span>List for sale</span></button>
                                                            </div>
                                                            
                                                            <div className='col-0 col-sm-6'>
                                                                <button className="sc-button loadmore style transfer fl-button pri-3 w-100"  onClick={()=>setTransferModalShow(true)}><span>Transfer</span></button>
                                                            </div>
                                                        </>
                                                    )}
                                                </>
                                            ) : (
                                                <>
                                                    <div className='col-0 col-sm-6'>
                                                        <button className="sc-button loadmore style bag fl-button pri-3 w-100"  onClick={() => setBuyModalShow(true)}><span>Buy Now</span></button>
                                                    </div>
                                                    {token.offers.length>0 && token.offers.filter(e => e.buyer_id == token.owner_id).length > 0 ?(
                                                        <div className='col-0 col-sm-6'>
                                                            <button className="sc-button loadmore style delist fl-button pri-3 w-100"  onClick={()=>setUnOfferModalShow(true)}><span>Cancel offer</span></button>
                                                        </div>
                                                    ):(
                                                        <div className='col-0 col-sm-6'>
                                                        <button className="sc-button loadmore style offer fl-button pri-3 w-100" onClick={() => setOfferModalShow(true)}><span>Place an offer</span></button>
                                                    </div>
                                                    )}
                                                </>
                                            )}
                                            
                                            </>
                                        ):(
                                            <Link to="/" onClick={checkAccount} className="sc-button loadmore style wallet fl-button pri-3"><span>Wallet connect</span></Link>
                                        )}
                                        
                                    </div>
                                    
                                    <div className="flat-tabs themesflat-tabs">
                                    <Tabs>
                                        <TabList>
                                        <Tab>Attributes</Tab>
                                        <Tab>Offers</Tab>
                                        <Tab>History</Tab>
                                        </TabList>
                                        <TabPanel>
                                            <div className="row attributes sc-card-product justify-content-center" style={{background: "unset", transform: "none"}}>
                                                {token?.metadata.attributes.map((attribue, index)=>(
                                                    <div className='col-0 col-sm-6 col-md-4' key={index}>
                                                        <div className='attributes-group'>
                                                            <span>{attribue.trait_type}</span>
                                                            <h5 className="mt-0"> <Link to="/">{attribue.value}</Link> </h5>
                                                            <h6 className="mt-2" style={{fontWeight: "400"}}>{token.contract_attributes? (parseInt(token.contract_attributes[attribue.trait_type][attribue.value]["count"])*100/token.contract_amount).toFixed(2) : 100}% have this trait</h6>
                                                        </div>
                                                    </div>
                                                ))}
                                            </div>
                                        </TabPanel>
                                        <TabPanel>
                                        <ul className="bid-history-list">
                                                {
                                                    token?.offers.length>0 ?(
                                                        token.offers.slice(0).reverse().map((item, index) => (
                                                            <li key={index} item={item}>
                                                                <div className="content">
                                                                    <div className="client">
                                                                        <div className="sc-author-box style-2">
                                                                            <div className="author-infor">
                                                                                <div className="name">
                                                                                    <h6><Link to={`/users/${item?.buyer_id}`}>{prettyTruncate(item?.buyer_id||"",16,`address`)} </Link></h6>
                                                                                </div>
                                                                                <span className="time">{item.issued_at}</span>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                    <div className="price">
                                                                        <h5>{prettyBalance(item.price)} NEAR</h5>
                                                                        <span>= ${prettyBalance(new BN(item.price) * store.nearUsdPrice, 24, 4)}</span>
                                                                    </div>
                                                                    {accountId && accountId==token.owner_id?(
                                                                        <div className=''>
                                                                            <button className="sc-button loadmore style accept fl-button pri-3 mb-0 px-0 py-1 py-sm-3 px-sm-5" onClick={()=>{setSelecteBuyer(item.buyer_id); setAcceptOfferModalShow(true)}}><span className=''>Accept</span></button>
                                                                        </div>
                                                                    ):(
                                                                        <></>
                                                                    )}
                                                                    
                                                                </div>
                                                            </li>
                                                        ))
                                                    ):(
                                                        <li>
                                                            <div className="content"><h6>No Offers Yet</h6></div>
                                                        </li>
                                                    )
                                                    
                                                }
                                                </ul>   
                                        </TabPanel>
                                            
                                        <TabPanel>
                                        {/* <Scrollbar style={{height: 250 }}> */}
                                                <ItemHistory contract_id={collectionId} token_id={token?.token_id||""}></ItemHistory>
                                        {/* </Scrollbar> */}
                                        </TabPanel>
                                    </Tabs>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <BuyModal
                title={token?.metadata.title || ""}
                token_id = {token?.token_id || ""}
                contract_id = {token?.contract_id || ""}
                price = {token?.price || ""}
                show={buymodalShow}
                onHide={() => setBuyModalShow(false)}
                handleBuy={handleBuy}
            />
            <OfferModal
                title={token?.metadata.title || ""}
                show={offermodalShow}
                onHide={() => setOfferModalShow(false)}
                handleOffer={handleOffer}
            />
            <AcceptOfferModal
                title={token?.metadata.title || ""}
                token_id = {token?.token_id || ""}
                contract_id = {token?.contract_id || ""}
                buyer_id = {selectedBuyer || ""}
                show={acceptoffermodalShow}
                onHide={() => setAcceptOfferModalShow(false)}
                handleAcceptOffer={handleAcceptOffer}
            />
            <CancelOfferModal
                title={token?.metadata.title || ""}
                show={unoffermodalShow}
                onHide={() => setUnOfferModalShow(false)}
                handleCancelOffer={handleCancelOffer}
            />
            <ListModal
                title={token?.metadata.title || ""}
                show={listmodalShow}
                onHide={() => setListModalShow(false)}
                handleList={handleListing}
            />
            <DelistModal
                title={token?.metadata.title || ""}
                show={delistmodalShow}
                onHide={() => setDelistModalShow(false)}
                handleDelist={handleDelist}
            />
            <TransferModal
                title={token?.metadata.title || ""}
                show={transfermodalShow}
                onHide={() => setTransferModalShow(false)}
                handleTransfer={handleTransfer}
            />
            <Footer />
        </div>
    );
}

export default ItemDetails01;
