import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useWalletSelector } from '../../hooks/WalletSelectorContext'
import OutsideClickHandler from "react-outside-click-handler";
import avt from '../../assets/images/avatar/avt-2.jpg'
import coin from '../../assets/images/icon/near-wallet-icon-removebg.png'
import near, {parseYoctoNear} from '../../lib/near'
import { prettyBalance, prettyTruncate } from "../../utils/common";
import useStore from '../../lib/store'

const User = ( {address} ) => {
    const [visible, setVisible] = useState(true);
    const store = useStore()
    const { accountId, setCommonModal, selector } = useWalletSelector()

    const onClickAvatar = (e) => {
        const header = document.querySelector('.js-avatar');
        if(header.classList)
            header.classList.contains('visible') ? header.classList.remove('visible') : header.classList.add('visible');
    };

    const onClickOutside = (e) => {
        const header = document.querySelector('.js-avatar');
        if(header.classList && header.classList.contains('visible'))
            header.classList.remove('visible')
    };

    const handleLogout = async(e) => {
        e.preventDefault()
        console.log('account', accountId)
        const wallet = await selector.wallet();
        wallet.signOut().catch((err) => {
            console.log("Failed to sign out");
            console.error(err);
          });
        if (!accountId) {
            
			//setCommonModal('login')
			return
		}
    }

    return (
        <OutsideClickHandler onOutsideClick={onClickOutside}>
            <div className="admin_active mt-2" id="header_admin" onClick={onClickAvatar}>
                <div className="header_avatar">
                    <div className="price">
                        <span>{store.userProfile? (prettyTruncate(store.userProfile.displayName,12)):(<>{prettyBalance(near.currentUser?near.currentUser.balance.total:"0")}<strong> </strong></>)} </span>
                    </div>
                    <img
                        className="avatar"
                        src={store.userProfile?store.userProfile.photo:avt}
                        alt="avatar"
                        />
                    <div className="avatar_popup mt-20 js-avatar">
                        <div className="d-flex align-items-center copy-text justify-content-between">
                            <p className="pe-2"> {address} </p>
                            <Link to="/" className="ml-2">
                                <p><i className="fal fa-copy"></i></p>
                            </Link>
                        </div>
                        <div className="d-flex align-items-center mt-10">
                            <img
                                className="img-fluid rounded coin"
                                src={coin}
                                alt="/"
                                style={{width: "50px", height: "50px"}}
                                />
                            <div className="info ml-10">
                                <p className="text-sm font-book text-gray-400">Balance</p>
                                <p className="w-full text-sm font-bold text-green-500">{near.currentUser?prettyBalance(near.currentUser.balance.total) :""} </p>
                            </div>
                        </div>
                        <div className="hr"></div>
                        <div className="links mt-10">
                            <div className="mt-1">
                                <Link to={`/users/${accountId}`}>
                                    <i className="fab fa-accusoft"></i> <p> My items</p>
                                </Link>
                            </div>
                            <div className="mt-2">
                                <Link className="mt-10" to="/edit-profile">
                                    <i className="fas fa-pencil-alt"></i> <p> Edit Profile</p>
                                </Link>
                            </div>
                            <div className="mt-2">
                                <a className="mt-10" href="/#" id="logout" onClick={handleLogout}>
                                    <i className="fal fa-sign-out"></i> <p> Logout</p>
                                </a>
                            </div>
                        </div>
                    </div>   
                </div>
            </div>
        </OutsideClickHandler>
    )
}

export default User;