import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom'
import { Modal } from "react-bootstrap";
import {prettyBalance} from "../../utils/common"
import near from '../../lib/near'

const CancelOfferModal = (props) => {

return (

    <Modal
      show={props.show}
      onHide={props.onHide}
      centered
    >
    <Modal.Header closeButton></Modal.Header>

    <div className="modal-body space-y-20 pd-40">
        <h3>Cancel Offer</h3>
        <p className="text-center"><span className="price color-popup">{props.title} </span>
        </p>
		<div className="d-flex justify-content-between">
            <p> Your Offer:</p>
            <p className="text-right price color-popup"> {props.price}  </p>
        </div>
        <button className="btn btn-primary" onClick={()=>props.handleCancelOffer()} data-toggle="modal" data-target="#popup_bid_success" data-dismiss="modal" aria-label="Close">Confirm </button>
    </div>
    </Modal>
    
  );
};

export default CancelOfferModal;
