import React , {useState, useEffect} from 'react';
import { Link } from 'react-router-dom'
import { Autoplay, EffectCoverflow, Pagination } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react.js';
import axios from 'axios'

import 'swiper/swiper.scss';
import 'swiper/modules/navigation/navigation.scss';
import 'swiper/modules/pagination/pagination.scss';
import shape1 from '../../assets/images/backgroup-secsion/bg-gradient1.png'
import shape2 from '../../assets/images/backgroup-secsion/bg-gradient2.png'
import shape3 from '../../assets/images/backgroup-secsion/bg-gradient3.png'
import defaultBGImg from '../../assets/images/backgroup-secsion/bg_section_h5.png';

const SliderStyle5 = () => {
    const [featureditem, setfeatureditem] = useState([]);

    const getfeatureditems = async () => {
        let initdata = await axios.get(`${process.env.REACT_APP_API_URL}/getfeatureditem`, {
            params: {},
        })
        if(initdata?.data.data != null){
            setfeatureditem(initdata.data.data.results);
        }
    }

    useEffect( () => {
        getfeatureditems();
    },[]);

    return (
        <div>
            <section className="flat-title-page style3 mainslider">
                <img className="bgr-gradient gradient1" src={shape1} alt="Astro" />
                <img className="bgr-gradient gradient2" src={shape2} alt="Astro" />
                <img className="bgr-gradient gradient3" src={shape3} alt="Astro" />
                <div className="overlay"></div>
                <div className="themesflat-container flex flex-column flex-md-row">
                    <div className='col-md-6 col-sm-12 pb-5' style={{ paddingTop: '100px'}}>
                        <div className="wrap-heading flat-slider flex flex-column flex-md-row">
                            <div className="content">
                                <h2 className="heading mt-15">Explore, collect, trade, create</h2>	
                                <h1 className="heading mb-style"><span className="tf-text s1">Digital collectibles on NEAR Protocol</span>                                          
                                </h1>
                                <h1 className="heading">AstroMarket</h1>
                                <p className="sub-heading mt-19 mb-40">NFT Marketplace on NEAR Protocol
                                </p>
                                <div className="flat-bt-slider flex style2">
                                    <Link to="/collections" className="sc-button header-slider style style-1 rocket fl-button pri-1"><span>Explore
                                    </span></Link>
                                    <a href="https://astrogen.io" target="_blank" className="sc-button header-slider style style-1 note fl-button pri-1"><span>Create
                                    </span></a>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='col-md-6 col-sm-12'>
                        <div>
                            <Swiper
                                modules={[ Autoplay, EffectCoverflow, Pagination ]}
                                effect={"coverflow"}
                                grabCursor={true}
                                pagination={true}
                                // spaceBetween={200}
                                slidesPerView={1}
                                coverflowEffect={{
                                    rotate: 50,
                                    stretch: 0,
                                    depth: 100,
                                    modifier: 1,
                                    slideShadows: true,
                                  }}
                                loop
                                autoplay={{
                                    delay: 1,
                                    disableOnInteraction: false,
                                    pauseOnMouseEnter: true,
                                }}
                                speed= {10000}
                                className="mySwiper"
                            >
                                {
                                    featureditem.length != 0 && featureditem?.map((item,index) => (
                                        <SwiperSlide key={index}>
                                            <div className='d-flex p-5 flex-column'>
                                                <div className='d-flex flex-column justify-content-evenly py-5 pe-5'>
                                                    <h2 style={{ color: 'white'}}>{item?.title}</h2>
                                                    <h5 className='mt-3' style={{ fontWeight: 400, color: '#b79ecf' }}>{item?.description}</h5>
                                                    <button className='px-4 py-3 mt-5 w-50'><a style={{ color: 'white'}} target="_blank" href={item?.linkedurl}>{item?.itemstatus==1? "Go to LaunchPad" : "Explore Collection" }</a></button>
                                                </div>
                                                <img src={item?.itemImg} alt="Astro"/>
                                            </div>
                                        </SwiperSlide>
                                    ))
                                }
                                {
                                    featureditem.length == 0 && 
                                    <>
                                        <SwiperSlide>
                                            <div className='my-5'>
                                                <img src={defaultBGImg} alt="Astro"/>
                                            </div>
                                        </SwiperSlide>
                                    </>
                                }
                            </Swiper>
                        </div>
                    </div>
                        
                        
                        
                    </div>
            </section>
        </div>
    );
}


export default SliderStyle5;
