import React from 'react';
import { Link } from 'react-router-dom'
import { Modal } from "react-bootstrap";

const CardModal = (props) => {
    
return (

    <Modal
      show={props.show}
      onHide={props.onHide}
      centered
    >
    <Modal.Header closeButton></Modal.Header>

    <div className="modal-body space-y-20 pd-40">
        <h3>Place an Offer</h3>
        <p className="text-center"><span className="price color-popup">{props.title} </span>
        </p>
        <input type="number" className="form-control"
            placeholder="00.00 NEAR" />
        <div className="d-flex justify-content-between">
            <p> Your Balance:</p>
            <p className="text-right price color-popup"> 10 NEAR </p>
        </div>
        <div className="d-flex justify-content-between">
            <p> Your Offer:</p>
            <p className="text-right price color-popup"> 10 NEAR </p>
        </div>
        <button className="btn btn-primary" data-toggle="modal" data-target="#popup_bid_success" data-dismiss="modal" aria-label="Close"> Place an offer</button>
    </div>
    </Modal>
    
  );
};

export default CardModal;
