import React , { useState , useEffect, Fragment } from 'react';
import { Link } from 'react-router-dom'
import CardModal from '../CardModal';

const ExploreItemView = ({data}) => {
    const [collectionId, setCollectionId] = useState("");
    const [modalShow, setModalShow] = useState(false);
    return (
        <Fragment>
            <div className='explore'>
                <div className="box-epxlore">
                    {data?.length>0 && 
                        data.map((item, index)=>(
                            <div className={`sc-card-product explode style2 mg-bt ${item.feature ? 'comingsoon' : '' } `} key={item.token_id}>
                                <div className="card-media explore-item">
                                    <Link to={`/collection/${item.contract_id}/${item.token_id}`}><img src={item?.media_url?.replace("ipfs://", "https://cloudflare-ipfs.com/ipfs/") || item.metadata.image.replace("ipfs://", "https://cloudflare-ipfs.com/ipfs/")} alt="Axies" /></Link>
                                    
                                </div>
                                <div className="card-title">
                                    <h5><Link to={`/collection/${item.contract_id}/${item.token_id}`}>"{item.metadata.name}"</Link></h5>
                                </div>
                                <div className="card-bottom style-explode">
                                    <div className="price">
                                        <h6>Price</h6>
                                        <div className="price-details">
                                            <big><span>{item?.price? item.price : 'Not Sale'}</span></big>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )
                    )}
                </div>
            </div>
            <CardModal
                show={modalShow}
                onHide={() => setModalShow(false)}
            />
        </Fragment>
    );
}

export default ExploreItemView;
