import React , { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import axios from 'axios'
import img1 from '../assets/images/box-item/imgactivity2.jpg'
import { InputGroup, FormControl, Button } from 'react-bootstrap';
import InfiniteScroll from 'react-infinite-scroll-component';
import Spinner from 'react-bootstrap/Spinner';

const Authors01 = () => {
    const USERS_PER_PAGE = 7;
    const [query, setQuery] = useState({page:0});
    const [loading, setLoading] = useState(false);
    const [hasMore, setHasMore] = useState(false); // Can load more tokens
    const [userdata, setuserdata] = useState([]);
    const [filterstring, setfilterstring] = useState('');

    const find = () => {
        fetchusers()
    }

    const beautifyAddr = (addr) => {
        if(addr ==  null)
            return ''
        if(addr.length > 12){
            let tmpstr = addr.slice(0,4) + '...' + addr.slice(addr.length - 6, addr.length)
            return tmpstr;
        }
        return addr
    }

    const fetchusers = async () => {
        let params = filterstring ? 
        {
            verified: true,
            filter: filterstring,
            __skip: query.page * USERS_PER_PAGE,
            __limit: USERS_PER_PAGE,
        } : 
        {
            verified: true,
            __skip: query.page * USERS_PER_PAGE,
            __limit: USERS_PER_PAGE,
        }
        let initdata = await axios.get(`${process.env.REACT_APP_API_URL}/getUsers`, {
            params
        })
        if(initdata?.data.data != null){
            setuserdata(initdata.data.data);
            if (initdata.data.data.length >= USERS_PER_PAGE) setHasMore(true);
        }
        setLoading(false);
    }

    const fetchMoreUsers = async () => {
        try {
            let params = filterstring ? 
            {
                verified: true,
                filter: filterstring,
                __skip: (query.page + 1) * USERS_PER_PAGE,
                __limit: USERS_PER_PAGE,
            } : 
            {
                verified: true,
                __skip: (query.page + 1) * USERS_PER_PAGE,
                __limit: USERS_PER_PAGE,
            }
            let initdata = await axios.get(`${process.env.REACT_APP_API_URL}/getUsers`, {
                params
            })

            const data = initdata.data.data;
    
            setQuery({ ...query, page: query.page + 1 });
            if (data.length < USERS_PER_PAGE) setHasMore(false);
            setuserdata([...userdata, ...data]);
        } catch (error) {
          console.log(error);
        }

    };

    //Init data
    useEffect( async () => {
        setLoading(true);
        await fetchusers();
    },[ query.filters, filterstring ]);

    return (
        <div className='authors'>
            <Header />
            <section className="flat-title-page inner">
                <div className="overlay"></div>
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="page-title-heading mg-bt-12">
                                <h1 className="heading text-center">Verified Users</h1>
                            </div>
                           
                        </div>
                    </div>
                </div>                    
            </section>

            <section className="tf-section our-creater dark-style2">
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-12 col-sm-12 col-md-12 mb-5 px-5">
                            <div className="widget widget-search mgbt-24">
                                <InputGroup className="mb-3">
                                    <FormControl
                                    placeholder="Please type user's name or address"
                                    aria-label="Enter Username"
                                    aria-describedby="basic-addon2"
                                    value={filterstring}
                                    onChange={(ev) => setfilterstring(ev.target.value)}
                                    />
                                    <Button variant="outline-secondary" onClick={find}>
                                    Find
                                    </Button>
                                </InputGroup>
                            </div>
                        </div>
                        {loading && (
                            <Spinner animation="border" role="status" variant="primary" style={{width:"100px", height:"100px", borderWidth: "thick", position:"relative", left: "50%"}}>
                                <span className="visually-hidden">Loading...</span>
                            </Spinner>
                        )}
                        <div className="box-epxlore">
                            <InfiniteScroll
                                dataLength={userdata.length}
                                next={() => fetchMoreUsers(query)}
                                hasMore={hasMore}
                                loader={
                                    <Spinner animation="border" role="status" variant="primary" style={{width:"60px", height:"60px", position:"relative", left: "50%"}}>
                                        <span className="visually-hidden">Loading...</span>
                                    </Spinner>
                                }
                                className="infinite-scroll-grid flex"
                                scrollThreshold={0.9}
                                style={{ height:'auto', overflow: 'hidden'}}
                            >
                            {
                                !loading && userdata?.map((item,index) => (
                                    <div className='px-5' key={index} >
                                        <div className="sc-card-activity style-3">
                                            <div className="content">
                                                <img className='media' src={item.photo? item.photo : img1} alt="" />
                                                <div className="infor">
                                                    <h4><Link to={`/users/${item.address}`}>{item.displayName}</Link></h4>
                                                    <div className="status">A : <span className="author">{beautifyAddr(item.address)}</span></div>
                                                    <div className="time mt-4" style={{ whiteSpace: 'nowrap', maxWidth: '150px', overflow: 'hidden', textOverflow: 'ellipsis', }}>{item.bio}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                ))
                            }
                            </InfiniteScroll>
                        </div>
                    </div>
                </div>
            </section>
            <Footer />
        </div>
    );
}

export default Authors01;
