import React , { useState, useEffect } from 'react';
import { Link, useParams } from 'react-router-dom';
import { Tab, Tabs, TabList, TabPanel  } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import CardModal from '../components/layouts/CardModal';
import ExploreItemView from '../components/layouts/explore-04/ExploreItemView';
import axios from 'axios';
import CollectionView from '../components/layouts/explore-02/CollectionView'
import defaultBannerUrl from '../assets/images/backgroup-secsion/option1_bg_profile.jpg';
import defaultPhotoUrl from '../assets/images/avatar/avt-2.jpg';

const Authors02 = () => {
    const {creatorId} = useParams();
    const [menuTab] = useState(
        [
            {
                class: 'active',
                name: 'Items'
            },
            {
                class: 'active',
                name: 'Collections'
            },
        ]
    )

    const [visible , setVisible] = useState(8);
    const showMoreItems = () => {
        setVisible((prevValue) => prevValue + 4);
    }

    const [modalShow, setModalShow] = useState(false);
    const [tokenInfo , setTokenInfo] = useState(null);
    const [collectionInfo , setCollectionInfo] = useState(null);
    const [upgraded, setupgraded] = useState(1);

    const getTokenInfo = async () => {
        try {
            const tokeninfo = await axios.get(`${process.env.REACT_APP_API_URL}/tokens`, {
                params: {
                    owner_id: creatorId
                },
		    })
            setTokenInfo(tokeninfo.data.data.results);
            setupgraded(Math.random()/9 + 1)
        } catch (error) {
            console.log(error);
            setTokenInfo(null);
        }
    };

    const getCollectionInfo = async () => {
        try {
            const tmpinfo = await axios.get(`${process.env.REACT_APP_API_URL}/collections`, {
                params: {
                    owner_id: creatorId
                },
		    })
            if(tmpinfo?.data?.data.results)
            setCollectionInfo(tmpinfo.data.data);
            setupgraded(Math.random()/9 + 1)
        } catch (error) {
            console.log(error);
            setCollectionInfo(null);
        }
    };

    useEffect(() => {
        console.log('creatorId', creatorId)
        if(!creatorId) return;
        getTokenInfo();
        getCollectionInfo();
    }, []);

    return (
        <div className='authors-2'>
            <Header />
            <section className="flat-title-page inner">
                <div className="overlay"></div>
                <div className="themesflat-container">
                    {
                        upgraded != 0 &&
                        <div className="flat-tabs tab-authors">
                            <div className="author-profile flex img-fluid" style={{ backgroundImage:`url(${collectionInfo?.results[0]?.user?.bannerImg ? collectionInfo.results[0].user.bannerImg : defaultBannerUrl })`}}>
                            </div>
                            <div className="sc-author-box style-2 feature-profile d-flex justify-content-center">
                                <img src={collectionInfo?.results[0]?.user?.photo ? collectionInfo.results[0].user.photo : defaultPhotoUrl } alt="Axies" className="mysuperavatar avatar"/>
                                {
                                    collectionInfo?.results[0]?.user?.verified ? 
                                    <div className='author-avatar' style={{ zIndex: 41, marginBottom: -40 }}>
                                        <div className="badge"></div>
                                    </div>
                                    :
                                    <></>
                                }
                            </div>
                            <div className="widget-social style-3 d-flex justify-content-end mysocial">
                                <ul>
                                    <li><a href={collectionInfo?.results[0]?.user?.customeUrl} target="_blank"><i className="fa fa-globe"></i></a></li>
                                    <li><a href={collectionInfo?.results[0]?.user?.twitter} target="_blank"><i className="fab fa-twitter"></i></a></li>
                                    <li><a href={collectionInfo?.results[0]?.user?.discord} target="_blank"><i className="fab fa-discord"></i></a></li>
                                    <li><a href={collectionInfo?.results[0]?.user?.instagram} target="_blank"><i className="fab fa-instagram"></i></a></li>
                                </ul>
                            </div>
                            <div>
                                <h2 className="title text-center mt-5 text-white">{creatorId}</h2>
                                <p className="content text-center text-white">{collectionInfo?.results[0]?.user?.bio}</p>
                            </div>
                        </div>
                    }
                </div>                    
            </section>
            <section className="authors tf-section pt-0">
                <div>
                    <div className="flat-tabs tab-authors tab-creator">
                        <Tabs>
                            <TabList className='d-flex justify-content-center'  style={{ boxShadow: '0px 3px 16px rgba(47, 83, 109, 0.12)' }}>
                                {
                                    menuTab.map((item,index) => (
                                        <Tab key={index}>{item.name}</Tab>
                                    ))
                                }
                            </TabList>

                            <div className="content-tab mb-4 themesflat-container">
                                <div className="content-inner">
                                    <div className="row">
                                        <TabPanel key={1}>
                                            <div className="col-xl-1 col-lg-1 col-md-0"></div>
                                            <div className="col-xl-10 col-lg-10 col-md-12">
                                                <ExploreItemView data={tokenInfo} />
                                            </div>
                                        </TabPanel>
                                        <TabPanel key={2}>
                                            <div className="col-xl-12 col-lg-12 col-md-12">
                                                <CollectionView data={collectionInfo?.results || []} />
                                            </div>
                                        </TabPanel>
                                    </div>
                                </div>
                            </div>
                        </Tabs>
                    </div>
                </div>
            </section>
            <CardModal
                show={modalShow}
                onHide={() => setModalShow(false)}
            />
            <Footer />
        </div>
    );
}

export default Authors02;
