import React , { useState , Fragment, useEffect } from 'react';
import PropTypes from 'prop-types'
import { Dropdown } from 'react-bootstrap';
import { Link } from 'react-router-dom'
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react.js';
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';
import Countdown from "react-countdown";
import CardModal from '../CardModal';
import img1 from '../../../assets/images/box-item/card-item8.jpg'
import imga1 from '../../../assets/images/avatar/avt-11.jpg'
import imgCollection1 from '../../../assets/images/avatar/avt-18.jpg'

import 'swiper/swiper.scss';
import 'swiper/modules/navigation/navigation.scss';
import 'swiper/modules/pagination/pagination.scss';
import axios from 'axios'
import {prettyTruncate} from '../../../utils/common'

const LiveAuction = () => {

    const [modalShow, setModalShow] = useState(false);
    const [userdata, setuserdata] = useState([]);

    const fetchrandomusers = async () => {
        let params = {
            random:true,
            __skip: 0,
            __limit: 10,
        }
        try {
            let initdata = await axios.get(`${process.env.REACT_APP_API_URL}/tokens`, {
                params
            })
            if(initdata?.data.data != null){
                setuserdata(initdata.data.data.results);
                // console.log('live nfts data', initdata.data.data.results)
            }
        } catch (error) {
            console.log(error);
            setuserdata([]);
        }
    }

    useEffect( async () => {
        await fetchrandomusers();
    },[]);

    return (
        <Fragment>
            <section className="tf-section live-auctions">
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="heading-live-auctions">
                                <h2 className="tf-title pb-20">
                                    Highest Sales</h2>
                                <Link to="/" className="exp style2">MORE</Link>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <Swiper
                                modules={[Navigation, Pagination, Scrollbar, A11y]}
                                spaceBetween={30}
                                breakpoints={{
                                    0: {
                                        slidesPerView: 1,
                                    },
                                    767: {
                                    slidesPerView: 3,
                                    },
                                    991: {
                                    slidesPerView: 4,
                                    },
                                    1300: {
                                        slidesPerView: 5,
                                    },
                                }}
                                navigation
                                pagination={{ clickable: true }}
                                scrollbar={{ draggable: true }}
                            >
                                {
                                    userdata?.map((item,index) => (
                                        <SwiperSlide key={index}>
                                            <div className="swiper-container show-shadow carousel auctions">
                                                <div className="swiper-wrapper">
                                                    <div className="swiper-slide">
                                                        <div className="slider-item">										
                                                            <div className="sc-card-product menu_card">
                                                                <div className="card-media">
                                                                    <Link to={`/collection/${item.contract_id}/${item.token_id}`}><img src={ item.media_url? item.media_url : item.metadata.image.replace("ipfs://", "https://cloudflare-ipfs.com/ipfs/") || img1 } alt="Axies" style={{ height: 'auto'}}/></Link>
                                                                    <Link to="/" className="wishlist-button heart"><span className="number-like"> {item.metadata.tokenId}</span></Link>

                                                                </div>
                                                                <div className="card-title">
                                                                    <h5 className='mw-100'><Link to={`/collection/${item.contract_id}/${item.token_id}`}>{item.metadata.title}</Link></h5>
                                                                    
                                                                </div>
                                                                <div className="meta-info">
                                                                    <div className="author">
                                                                        <div className="info">
                                                                            <span>Owned By</span>
                                                                            <h6> <Link to={`/users/${item.owner_id}`}>{prettyTruncate(item.owner_id, 20, "address")}</Link> </h6>
                                                                        </div>
                                                                    </div>
                                                                    <div className="price">
                                                                        <span>Price</span>
                                                                        <h5>{item?.price || ""}</h5>
                                                                    </div>
                                                                </div>
                                                            </div>      	
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </SwiperSlide>
                                    ))
                                }
                            </Swiper>
                        </div>
                    </div>
                </div>
            </section>
            <CardModal
                show={modalShow}
                onHide={() => setModalShow(false)}
            />
        </Fragment>
    );
}

export default LiveAuction;
