import './App.css';
import { useEffect } from 'react';
import { Routes , Route, useNavigate } from 'react-router-dom';
import axios from 'axios'
import { useNativeSearchParams } from './hooks/useSearchParams';
import useStore from './lib/store'
import routes from './pages/index'
import WalletHelper from './lib/WalletHelper'

function App() {
    const store = useStore()
    const query = useNativeSearchParams();
    const navigate  = useNavigate();

    const getNearUsdPrice = async () => {
		try {
			const nearUsdPrice = await axios.get(
				'https://api.coingecko.com/api/v3/simple/price?ids=NEAR&vs_currencies=USD'
			)
			store.setNearUsdPrice(nearUsdPrice.data.near.usd)
			
		} catch (error) {
			console.error('Failed Coingecko')
		}
	}

    const setupUser = async (currentUser) => {
		if (!currentUser) return

		store.setCurrentUser(currentUser.accountId)
		store.setUserBalance(currentUser.balance)


		const userProfileResp = await axios.get(`${process.env.REACT_APP_API_URL}/profiles`, {
			accountId: currentUser.accountId,
		})
		const userProfileResults = userProfileResp.data.data.results

		if (userProfileResults.length === 0) {
			
		} else {
			const userProfile = userProfileResults[0]
			store.setUserProfile(userProfile)

		}

	}

    const _init = async () => {
		//await WalletHelper.initialize({ onChangeUser: setupUser })

		//const currentUser = WalletHelper.currentUser

		// if (currentUser) {
		// 	setupUser(currentUser)
		// }
		getNearUsdPrice()
		store.setInitialized(true)
	}

    useEffect(() => {
        _init()
        //let newUrl = window.location.origin + window.location.hash;
        //window.history.pushState(null, null, newUrl);
        // navigate('/home')
    },[]);
    return (
        <Routes >
            {
            routes.map((data,index) => (
                <Route onUpdate={() => window.scrollTo(0, 0)} exact={true} path={data.path} element={data.component} key={index} />
            ))
            }
        </Routes>
    );
}

export default App;
