import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom'
import { Modal } from "react-bootstrap";
import {prettyBalance} from "../../utils/common"
import near from '../../lib/near'

const ListModal = (props) => {
    const [price, setPrice] = useState("");

return (

    <Modal
      show={props.show}
      onHide={props.onHide}
      centered
    >
    <Modal.Header closeButton></Modal.Header>

    <div className="modal-body space-y-20 pd-40">
        <h3>List a NFT on the market</h3>
        <p className="text-center"><span className="price color-popup">{props.title} </span>
        </p>
        <input type="number" className="form-control"
            placeholder="00.00 NEAR" onChange={(e) => setPrice(e.target.value)} value={price}/>
        <div className="d-flex justify-content-between">
            <p> Your Balance:</p>
            <p className="text-right price color-popup"> {prettyBalance(near?.currentUser?.balance?.total||"0")}  </p>
        </div>
        <div className="d-flex justify-content-between">
            <p> Price:</p>
            <p className="text-right price color-popup"> {price}  </p>
        </div>
        <button className="btn btn-primary" onClick={()=>props.handleList(price)} data-toggle="modal" data-target="#popup_bid_success" data-dismiss="modal" aria-label="Close"> List NFT</button>
    </div>
    </Modal>
    
  );
};

export default ListModal;
