import React , {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import defaultPhotoUrl from '../assets/images/avatar/avt-2.jpg';
import axios from 'axios'
import InfiniteScroll from 'react-infinite-scroll-component';
import Spinner from 'react-bootstrap/Spinner';
import Select from '../components/Common/Select';
import { prettyBalance, prettyTruncate, parseFilter, parseTimeForFilter, unParseFilter, parseDateFilter } from '../utils/common'

const MyActivity = () => {
    const [dataFilter, setdataFilter] = useState(
        [
            {
                icon: 'icon-fl-sort-filled',
                name: 'Listings',
                state: false
            },
            {
                icon: 'icon-fl-discount',
                name: 'Sales',
                state: false
            },
            {
                icon: 'icon-fl-logout',
                name: 'Transfers',
                state: false
            },
            {
                icon: 'icon-fl-credit-card',
                name: 'Offers',
                state: false
            },
        ]
    )

    const ACTIVITIES_PER_PAGE = 10;

    const [activities, setactivities] = useState([]);
    const [loading, setloading] = useState(false);
    const [query, setQuery] = useState({page:0});
    const [hasMore, setHasMore] = useState(false); // Can load more tokens
    const [filterdate, setfilterdate] = useState('Last 7 Days');

    const fetchMoreActivities = async () => {
        try {
            let tmpfilters = []
            for (let i = 0; i < dataFilter.length; i++) {
                if(dataFilter[i].state)
                    tmpfilters.push(unParseFilter(dataFilter[i].name))
            }
            if(tmpfilters.length == 0)
                setQuery({page: 0})
            if(tmpfilters.length > 0)
                setQuery({page: 0, params: { filter:tmpfilters.join('||') }})
            const resCollections = await axios.get(`${process.env.REACT_APP_API_URL}/activities`, {
                params: {
                    ...query.params,
                    datefilter: parseDateFilter(filterdate),
                    __skip: (query.page + 1) * ACTIVITIES_PER_PAGE,
                    __limit: ACTIVITIES_PER_PAGE,
                },
		    })

            const data = await resCollections.data.data.results;
    
            setQuery({ ...query, page: query.page + 1 });
        
            if (data.length < ACTIVITIES_PER_PAGE) setHasMore(false);
            const newData = [...activities, ...data]
            setactivities(newData);
        } catch (error) {
          console.log(error);
        }

    };

    const hitFilter = async (index) => {
        let tmpdata = dataFilter;
        tmpdata[index].state = !dataFilter[index].state;
        setdataFilter(tmpdata)
        getActivities(tmpdata)
    }

    const clearFilter = () => {
        let tmpdata = dataFilter;
        for (let i = 0; i < tmpdata.length; i++) {
            tmpdata[i].state = false
        }
        setdataFilter(tmpdata)
        getActivities();
    }

    const getActivities = async (tmpdata) => {
        setloading(true);
        let tmpfilters = []
        let tmpfilterdata;
        if(tmpdata)
            tmpfilterdata = tmpdata;
        else
            tmpfilterdata = dataFilter;

        for (let i = 0; i < tmpfilterdata.length; i++) {
            if(tmpfilterdata[i].state)
                tmpfilters.push(unParseFilter(tmpfilterdata[i].name))
        }
        setQuery({page: 0, params: { filter:tmpfilters.join('||') }})
        let initdata = await axios.get(`${process.env.REACT_APP_API_URL}/activities`, {
            params: {
                filter: tmpfilters.join('||'),
                datefilter: parseDateFilter(filterdate),
                __skip: 0 * ACTIVITIES_PER_PAGE,
                __limit: ACTIVITIES_PER_PAGE,
            },
        })
        if(initdata?.data.data != null){
            setactivities(initdata.data.data.results);
            if (initdata.data.data.results.length >= ACTIVITIES_PER_PAGE) setHasMore(true);
        }
        setloading(false);
    }

    useEffect( () => {
        getActivities();
    },[filterdate]);

    return (
        <div>
            <Header />
            <section className="flat-title-page inner">
                <div className="overlay"></div>
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="page-title-heading mg-bt-12">
                                <h1 className="heading text-center">Activity</h1>
                            </div>
                        </div>
                    </div>
                </div>                    
            </section>
            <section className="tf-activity s1 tf-section">
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-lg-9 col-md-9 col-12">
                            <div className="table-ranking">
                                <div className="flex th-title">
                                    <div className="column1">
                                        <h3>Items</h3>
                                    </div>
                                    <div className="column">
                                        <h3>Price</h3>
                                    </div>
                                    <div className="column">
                                    <h3>From</h3>
                                    </div>
                                    <div className="column">
                                    <h3>To</h3>
                                    </div>
                                    <div className="column">
                                    <h3>Time</h3>
                                    </div>
                                    <div className="column">
                                    <h3>Type</h3>
                                    </div>
                                </div>
                                <div style={{ overflowY: 'hidden'}}>
                                {loading && (
                                    <Spinner animation="border" role="status" variant="primary" style={{width:"100px", height:"100px", borderWidth: "thick", position:"relative", left: "50%"}}>
                                        <span className="visually-hidden">Loading...</span>
                                    </Spinner>
                                )}
                                {!loading && (
                                    <InfiniteScroll
                                        dataLength={activities.length}
                                        next={() => fetchMoreActivities()}
                                        hasMore={hasMore}
                                        loader={
                                            <Spinner animation="border" role="status" variant="primary" style={{width:"40px", height:"40px", position:"relative", left: "50%"}}>
                                                <span className="visually-hidden">Loading...</span>
                                            </Spinner>
                                        }
                                        className="infinite-scroll-grid flex pt-5"
                                        scrollThreshold={0.9}
                                        style={{ height:'auto', overflow: 'hidden'}}
                                    >
                                        {   
                                            activities.map((item,index) => (
                                                <div key={index} className="fl-item2 px-2" style={{ width: '95%' }}>
                                                    <div className="item flex">
                                                        <div className="infor-item flex column1">
                                                            <div className="media d-flex">
                                                                <img className="rounded" style={{ objectFit: 'cover' }} src={item?.tokens[0]?.media_url? item.tokens[0].media_url : defaultPhotoUrl} alt="Axies" />
                                                            </div>
                                                            <div className="content-collection align-self-center px-4">
                                                                <h5 className="title"><Link to={`/collection/${item?.contract_id}`}>{prettyTruncate(item?.contract_id, 14, 'address')}</Link></h5>
                                                                <h5 className="title"><Link to={`/collection/${item?.contract_id}/${item?.token_id}`}>{prettyTruncate(item?.tokens[0]?.metadata.title||'', 16) }</Link></h5>
                                                            </div>
                                                        </div>
                                                        <div className="column">
                                                            <span>{prettyBalance(item?.price||'')} </span>
                                                        </div>
                                                        <div className="column td2">
                                                            <span><Link to={`/users/${item?.from}`} className='text-success'>{ prettyTruncate(item?.from, 10, 'address') }</Link></span>
                                                        </div>
                                                        <div className="column td3">
                                                            <span><Link to={`/users/${item?.to}`} className='text-danger'>{ prettyTruncate(item?.to, 10, 'address') }</Link></span>
                                                        </div>
                                                        <div className="column td4">
                                                            <span><small>{parseTimeForFilter(item?.issued_at) }</small></span>
                                                        </div>
                                                        <div className="column td5">
                                                            <span>&nbsp;&nbsp;&nbsp;&nbsp;{ parseFilter(item?.type) }</span>
                                                        </div>
                                                    </div>
                                                </div>
                                            ))
                                        }
                                    </InfiniteScroll>
                                )}
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-3 col-md-3 col-12">
                            <div id="side-bar" className="side-bar style-2">
                                <div className='mb-4 mt-2'>
                                    <h3 className="mb-3">Sort by</h3>
                                    <div className='seclect-box p-2'>
                                        <Select value={filterdate} setValue={setfilterdate} options={['Last 7 Days', 'Last 30 Days', 'All Time']} />
                                    </div>
                                </div>
                                <div className="widget widget-filter style-2 mgbt-0">
                                    <h3 className="title-widget">Filter</h3>
                                    <ul className="box-check">
                                        {
                                            dataFilter.map((item,index) => (
                                                !item.state ?
                                                <li key={index} onClick={() => hitFilter(index)}><a className="box-widget-filter" style={{ cursor: 'pointer'}}><i className={item.icon}></i>{item.name}</a></li>
                                                :
                                                <li key={index} onClick={() => hitFilter(index)}><a className="box-widget-filter active" style={{ cursor: 'pointer'}}><i className={item.icon}></i>{item.name}</a></li>
                                            ))
                                        }
                                    </ul>
                                    <a onClick={clearFilter} className="clear-check btn-filter style-2" >
                                        Clear All Filters
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Footer />
        </div>
    );
}

export default MyActivity;
