import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import { useWalletSelector } from '../hooks/WalletSelectorContext'
import axios from 'axios'
import { BreadcrumbItem, Form, InputGroup } from 'react-bootstrap'
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import defaultBannerUrl from '../assets/images/backgroup-secsion/option1_bg_profile.jpg';
import defaultPhotoUrl from '../assets/images/avatar/avt-2.jpg';

var FormData = require('form-data');

const EditProfile = () => {
    const navigate = useNavigate();
    const { accountId, setCommonModal } = useWalletSelector()
    //user collection field
    const [photo, setphoto] = useState("");
    const [displayName, setdisplayName] = useState("");
    const [customeUrl, setcustomeUrl] = useState("");
    const [email, setemail] = useState("");
    const [bio, setbio] = useState("");
    const [twitter, settwitter] = useState("");
    const [discord, setdiscord] = useState("");
    const [instagram, setinstagram] = useState("");
    const [bannerImg, setbannerImg] = useState("");

    //update user collection field button function
    const updateProfile = async () => {
        let status = false;
        status = checkDataField([accountId, displayName, email, bio, twitter ])
        if (status) 
        {
            let updateData = { accountId, photo, displayName, customeUrl, email, bio, twitter, discord, instagram, bannerImg }
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/updateUser`, {
                params: {
                    ...updateData,
                    "verified": false
                },
            })
            toast('Successfully Updated!',  {
                position: toast.POSITION.BOTTOM_RIGHT,
                className: 'foo-bar'
            })
        }
    }

    const uploadphoto = (ev) => {
        const file = ev.target.files[0];
        const url = `https://api.pinata.cloud/pinning/pinFileToIPFS`;
        const formData = new FormData();
        formData.append("file",file);
        return axios.post(url,formData,{
            headers:{
                'Content-Type': `multipart/form-data; boundary= ${formData._boundary}`,
                'pinata_api_key':'fe3060199e1632e4d52a',
                'pinata_secret_api_key':'1f4a05527f4e641e6fe037affea5fb577196dd8c34f7db093c0d27102f3e6ae7',
            }
        }).then(function (response) {
                setphoto("https://nearnaut.mypinata.cloud/ipfs/" + response.data.IpfsHash)
                console.log('photo', "https://nearnaut.mypinata.cloud/ipfs/" + response.data.IpfsHash)
        }).catch(function (error) {
            console.log('cant upload photo',error)
        });
    }

    const uploadbanner = (ev) => {
        const file = ev.target.files[0];
        const url = `https://api.pinata.cloud/pinning/pinFileToIPFS`;
        const formData = new FormData();
        formData.append("file",file);
        return axios.post(url,formData,{
            headers:{
                'Content-Type': `multipart/form-data; boundary= ${formData._boundary}`,
                'pinata_api_key':'fe3060199e1632e4d52a',
                'pinata_secret_api_key':'1f4a05527f4e641e6fe037affea5fb577196dd8c34f7db093c0d27102f3e6ae7',
            }
        }).then(function (response) {
            setbannerImg("https://nearnaut.mypinata.cloud/ipfs/" + response.data.IpfsHash)
            console.log('banner', "https://nearnaut.mypinata.cloud/ipfs/" + response.data.IpfsHash)
        }).catch(function (error) {
            console.log('cant upload banner',error)
        });
    }

    const deletephoto = () => {
        setphoto("")
    }

    const checkDataField = (datalist) => {
        for (let index = 0; index < datalist.length; index++) {
            const element = datalist[index];
            if(element == ""){
                return false;
            }
        }
        return true;
    }
    
    //Init data
    useEffect( async () => {
        if(!accountId) {
            navigate("/");
            return
        }
        let initdata = await axios.get(`${process.env.REACT_APP_API_URL}/getUser`, {
            params: {
                accountId,
            },
        })
        if(initdata?.data.data != null){
            //get information of address
            const maindata = initdata.data.data;
            setphoto(maindata.photo);
            setbio(maindata.bio);
            setcustomeUrl(maindata.customeUrl);
            setdiscord(maindata.discord);
            setdisplayName(maindata.displayName);
            setemail(maindata.email);
            settwitter(maindata.twitter);
            setinstagram(maindata.instagram);
        }
    },[accountId]);

    return (
        <div>
            <Header />
            <section className="flat-title-page inner">
                <div className="overlay"></div>
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="page-title-heading mg-bt-12">
                                <h1 className="heading text-center">Edit Profile</h1>
                            </div>
                        </div>
                    </div>
                </div>                    
            </section>
            <div className="tf-create-item tf-section">
                <div className="themesflat-container">
                    <div className="row">
                         <div className="col-xl-3 col-lg-4 col-md-12 col-12">
                            <div className="sc-card-profile text-center">
                                <div className="card-media">
                                    {photo=="" ? 
                                    <img id="profileimg" src={defaultPhotoUrl} alt="Axies" />
                                    :
                                    <img id="profileimg" src={photo} alt="Axies" style={{ margin: '20px 0px'}}/>
                                    }
                                </div>
                                <div id="upload-profile">
                                    <Link to="#" className="btn-upload">Upload New Photo</Link>
                                    <input id="tf-upload-img" type="file" name="profile" onChange={(ev) => uploadphoto(ev)}/>
                                </div>
                                <button className="btn-upload style2 border-0" onClick={deletephoto}>
                                    Delete
                                </button>
                            </div>
                         </div>
                         <div className="col-xl-9 col-lg-8 col-md-12 col-12">
                            <div className="form-upload-profile">
                                <h4 className="title-create-item">Choice your Cover image</h4>
                                <div className="option-profile clearfix row">
                                    <div className="sc-card-profile text-center col-12 col-sm-3">
                                        <div id="upload-profile">
                                            <Link to="" className="btn-upload">Upload Background</Link>
                                            <input id="tf-upload-img" type="file" name="profile" onChange={(ev) => uploadbanner(ev)}/>
                                        </div>
                                        <button className="btn-upload style2 border-0" onClick={ () => setbannerImg("")} >
                                            Delete
                                        </button>
                                    </div>
                                    <div className="image col-12 col-sm-8">
                                        <img src={bannerImg!=""? bannerImg : defaultBannerUrl} alt="Axies" />
                                    </div>
                                </div>
                                <div className="form-profile">
                                    <div className="form-infor-profile">
                                        <div className="info-account">
                                            <h4 className="title-create-item">Account info</h4>
                                            <InputGroup hasValidation className='mb-5'>
                                                <InputGroup.Text className='col-3'><p style={{ fontSize: '1.5rem'}} className='m-auto'>Display Name</p></InputGroup.Text>
                                                <Form.Control type="text" placeholder="Trista Francis" value={displayName} onChange={(ev) => setdisplayName(ev.target.value)} required isInvalid={displayName=="" ? true : false} />
                                                <Form.Control.Feedback type="invalid">
                                                    <div style={{ fontSize: '1.3rem'}}>Please type display name.</div>
                                                </Form.Control.Feedback>
                                            </InputGroup>
                                            <InputGroup hasValidation className='mb-5'>
                                                <InputGroup.Text className='col-3'><p style={{ fontSize: '1.5rem'}} className='m-auto'>Website</p></InputGroup.Text>
                                                <Form.Control type="text" placeholder="Axies.Trista Francis.com/" value={customeUrl} onChange={(ev) => setcustomeUrl(ev.target.value)} />
                                                <Form.Control.Feedback type="invalid">
                                                    <div style={{ fontSize: '1.3rem'}}>Please type website url.</div>
                                                </Form.Control.Feedback>
                                            </InputGroup>
                                            <InputGroup hasValidation className='mb-5'>
                                                <InputGroup.Text className='col-3'><p style={{ fontSize: '1.5rem'}} className='m-auto'>Email</p></InputGroup.Text>
                                                <Form.Control type="email" placeholder="Enter your email" value={email} onChange={(ev) => setemail(ev.target.value)} required isInvalid={email =="" ? true : false} />
                                                <Form.Control.Feedback type="invalid">
                                                    <div style={{ fontSize: '1.3rem'}}>Please type valid email.</div>
                                                </Form.Control.Feedback>
                                            </InputGroup>
                                            <InputGroup hasValidation className='mb-5'>
                                                <InputGroup.Text className='col-3'><p style={{ fontSize: '1.5rem'}} className='m-auto'>Bio</p></InputGroup.Text>
                                                <Form.Control as="textarea" tabIndex="4" rows="5" value={bio} onChange={(ev) => setbio(ev.target.value)} required isInvalid={bio =="" ? true : false} />
                                                <Form.Control.Feedback type="invalid">
                                                    <div style={{ fontSize: '1.3rem'}}>Please type your bio information.</div>
                                                </Form.Control.Feedback>
                                            </InputGroup>
                                        </div>
                                        <div className="info-social">
                                            <h4 className="title-create-item">Your Social media</h4>
                                                <InputGroup hasValidation className='mb-5'>
                                                    <InputGroup.Text className='col-3'><div style={{ fontSize: '1.5rem' }} className='m-auto'>Twitter</div></InputGroup.Text>
                                                    <Form.Control type="text" placeholder="Twitter URL" value={twitter} onChange={(ev) => settwitter(ev.target.value)} required isInvalid={twitter =="" ? true : false} />
                                                    <Form.Control.Feedback type="invalid">
                                                        <div style={{ fontSize: '1.3rem'}}>Please type twitter URL.</div>
                                                    </Form.Control.Feedback>
                                                </InputGroup>
                                                <InputGroup hasValidation className='mb-5'>
                                                    <InputGroup.Text className='col-3'><div style={{ fontSize: '1.5rem' }} className='m-auto'>Discord</div></InputGroup.Text>
                                                    <Form.Control type="text" placeholder="Discord URL" value={discord} onChange={(ev) => setdiscord(ev.target.value)}/>
                                                </InputGroup>
                                                <InputGroup hasValidation className='mb-5'>
                                                    <InputGroup.Text className='col-3'><div style={{ fontSize: '1.5rem' }} className='m-auto'>Instagram</div></InputGroup.Text>
                                                    <Form.Control type="text" placeholder="Instagram URL" value={instagram} onChange={(ev) => setinstagram(ev.target.value)}/>
                                                </InputGroup>
                                        </div> 
                                    </div>
                                    <button className="tf-button-submit mg-t-15" onClick={ updateProfile }>
                                        Update Profile
                                    </button>
                                </div>
                            </div>
                         </div>
                    </div>
                </div>
            </div>
            <Footer />
        </div>
    );
}

export default EditProfile;
