import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom'
import { Modal } from "react-bootstrap";
import {prettyBalance} from "../../utils/common"
import near from '../../lib/near'

const DelistModal = (props) => {

return (

    <Modal
      show={props.show}
      onHide={props.onHide}
      centered
    >
    <Modal.Header closeButton></Modal.Header>

    <div className="modal-body space-y-20 pd-40">
        <h3>Confirm Delist</h3>
        <p className="text-center"><span className="price color-popup">{props.title} </span>
        </p>
        <button className="btn btn-primary" onClick={()=>props.handleDelist()} data-toggle="modal" data-target="#popup_bid_success" data-dismiss="modal" aria-label="Close">Delist</button>
    </div>
    </Modal>
    
  );
};

export default DelistModal;
