import React , { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import axios from 'axios';
import { prettyBalance } from '../utils/common';
import Spinner from 'react-bootstrap/Spinner';

const Ranking = () => {
    const [visible , setVisible] = useState(6);
    const showMoreItems = () => {
        setVisible((prevValue) => prevValue + 3);
    }
    const [rankdata, setrankdata] = useState(null);
    const [loading, setLoading] = useState(false);
    const [hasMore, setHasMore] = useState(false); // Can load more tokens
    
    const getRankings = async () => {
        setLoading(true);
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/rankings`, {
        })
        console.log('ranking data', res?.data?.data)
        if(res?.data?.data){
            setrankdata(makeData(res?.data?.data));
        }
        setLoading(false);
    }
    
    const makeData = (data) => {
        let tmparray = [];
        for (let i = 0; i < data.length; i++) {
            const element = data[i];
            let tmpdata = {
                banimg_url: element.banimg_url,
                contract_id: element.contract_id,
                total_count: element.total_count,
                name: element.name,
            }
            if(element.stats.length > 0){
                tmpdata.total_volumn = element.stats[0].total_volumn;
                tmpdata.floor_price = element.stats[0].floor_price;
                if(element.stats.length == 1){
                    tmpdata.change_1 = 0;
                    tmpdata.change_7 = 0;
                }else{
                    let change_7d;
                    if(parseFloat(element.stats[element.stats.length - 1].total_volumn) == 0)
                        change_7d = 100;
                    else
                        change_7d = parseFloat(element.stats[0].total_volumn) / 
                        parseFloat(element.stats[element.stats.length - 1].total_volumn) * 100;
                    tmpdata.change_7 = change_7d.toFixed(0);
                    let lastDay = element.stats[0].issued_at;
                    let today = new Date();
                    if(today.toISOString().split('T')[0] == lastDay){
                        tmpdata.change_1 = parseFloat(element.stats[0].total_volumn) / 
                        parseFloat(element.stats[1].total_volumn) * 100;
                    }else{
                        tmpdata.change_1 = 0;
                    }
                }
            }else{
                tmpdata.total_volumn = 0;
                tmpdata.floor_price = 0;
                tmpdata.change_1 = 0;
                tmpdata.change_7 = 0;
            }
            tmparray.push(tmpdata);
        }
        console.log('made data', tmparray)
        return tmparray
    }

    useEffect(() => {
        getRankings();
    }, []);

    return (
        <div>
            <Header />
            <section className="flat-title-page inner">
                <div className="overlay"></div>
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="page-title-heading mg-bt-12">
                                <h1 className="heading text-center">Ranking</h1>
                            </div>
                        </div>
                    </div>
                </div>                    
            </section>
            <section className="tf-section tf-rank">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="table-ranking mx-5">
                                <div className="flex th-title">
                                    <div className="column1">
                                        <h3>Collection</h3>
                                    </div>
                                    <div className="column">
                                        <h3>Volume</h3>
                                    </div>
                                    <div className="column">
                                    <h3>24h %</h3>
                                    </div>
                                    <div className="column">
                                    <h3>7d %</h3>
                                    </div>
                                    <div className="column">
                                    <h3>Floor Price</h3>
                                    </div>
                                    <div className="column">
                                    <h3>Assets</h3>
                                    </div>
                                </div>
                                <div style={{ overflowY: 'hidden' }}>
                                    {loading && (
                                        <Spinner animation="border" role="status" variant="primary" style={{width:"100px", height:"100px", borderWidth: "thick", position:"relative", left: "50%"}}>
                                            <span className="visually-hidden">Loading...</span>
                                        </Spinner>
                                    )}
                                    {
                                        !loading && rankdata?.slice(0,visible).map((item,index) => (
                                            <div key={index} className="fl-item2" style={{ width: '95%' }}>
                                                <div className="item flex">
                                                    <div className="infor-item flex column1">
                                                        <div className="media">
                                                            <img src={item.banimg_url} alt="Axies" />
                                                        </div>
                                                        <div className="d-flex ps-4" style={{ flexDirection: 'column', justifyContent: 'center' }}>
                                                            <h5><Link to={`/collection/${item.contract_id}`}>"{item.name}"</Link></h5>
                                                        </div>
                                                    </div>
                                                    <div className="column" style={{ textAlign: 'center' }}>
                                                        <span>{prettyBalance(item.total_volumn)} Near</span>
                                                    </div>
                                                    <div className="column td2 text-center">
                                                        <span>{item.change_1} %</span>
                                                    </div>
                                                    <div className="column td3 text-center">
                                                        <span>{item.change_7} %</span>
                                                    </div>
                                                    <div className="column td4 text-center">
                                                        <span>{prettyBalance(item.floor_price)} Near</span>
                                                    </div>
                                                    <div className="column td6">
                                                        <span>{item.total_count}</span>
                                                    </div>
                                                </div>
                                            </div>
                                        ))
                                    }
                                    {
                                        visible < rankdata?.length && 
                                        <div className="col-md-12 wrap-inner load-more text-center"> 
                                            <Link to="#" id="load-more" className="sc-button loadmore fl-button pri-3" onClick={showMoreItems}><span>Load More</span></Link>
                                        </div>
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Footer />
        </div>
    );
}

export default Ranking;
